export interface AlertsProps {}

export const INFO_ALERT = 'info';
export const ERROR_ALERT = 'error';
export const SUCCESS_ALERT = 'success';
export const WARNING_ALERT = 'warning';
export const USER_ALERT = 'user';
export const CONFIG_ALERT = 'config';

export interface AlertData {
  type:
    | typeof INFO_ALERT
    | typeof ERROR_ALERT
    | typeof WARNING_ALERT
    | typeof SUCCESS_ALERT
    | typeof USER_ALERT
    | typeof CONFIG_ALERT;
  title: string;
  onConfirmLabel?: string;
  onCancelLabel?: string;
  subMessage?: string;
  onConfirm?: () => void;
  onCancel?: () => void;
  onClose?: () => void;
  testId?: string;
}

export type AlertButtonColors =
  | typeof INFO_ALERT
  | typeof ERROR_ALERT
  | typeof WARNING_ALERT
  | typeof SUCCESS_ALERT;
