import React from 'react';
import { FilterCardProps } from './types';
import Collapsible from '../Collapsible/Collapsible';
import styles from './styles.module.scss';
import { ReactComponent as IcoFilter } from '../assets/ico-filter.svg';

const FilterCard: React.FC<FilterCardProps> = (props) => {
  const { children, classes, openState, t, testId } = props;
  return (
    <div
      id="filter-id"
      className={classes.concat(styles.filters).join(' ')}
      data-testid={testId}
    >
      <Collapsible openState={openState}>
        <div className={styles.filterHeader}>
          <div className={styles.filterTitle}>
            <IcoFilter />
            <span>{t('FilterCard:title')}</span>
          </div>
        </div>
        <div>{children}</div>
      </Collapsible>
    </div>
  );
};

export default FilterCard;
