import React, { useCallback, useEffect, useState } from 'react';
import Slider from './components/Slider/Slider';
import { MultiImageInputProps } from './types';
import styles from './styles.module.scss';
import ImageGrid from './components/ImageGrid/ImageGrid';
import { $alerts } from '../Alerts/Alerts';
import { ReactComponent as WarningIcon } from '../assets/warning.svg';
import EmptyStatus from './components/EmptyStatus/EmptyStatus';

const MultiImageInput: React.FC<MultiImageInputProps> = ({
  initialValue,
  readOnly,
  onChange,
  onDelete,
  placeholder,
  t,
  helperText,
}) => {
  const index = useState(0);
  const value = useState<string[]>([]);
  const [files, setFiles] = value;
  const [, setIndex] = index;
  const [slides, setSlides] = useState<string[]>([...files, ...initialValue]);
  const [error, setError] = useState(false);

  useEffect(() => {
    setSlides([...files, ...initialValue]);
  }, [files, initialValue]);

  const deleteSlide = useCallback(
    (i: number) => () => {
      setSlides((sSlides) => {
        sSlides.splice(i, 1);
        setIndex((prevIndex) => {
          if (prevIndex < sSlides.length) {
            return prevIndex;
          }
          if (prevIndex - 1 >= 0) {
            return prevIndex - 1;
          }
          return 0;
        });
        return [...sSlides];
      });
      if (i < files.length) {
        setFiles(files.filter((x, idx) => i !== idx));
      } else {
        onDelete(i - files.length);
      }
    },
    [setIndex, files, setFiles, onDelete],
  );

  const onDeleteSlide = useCallback(
    (i: number) => {
      $alerts.next({
        type: 'warning',
        title: 'Eliminar imagen',
        subMessage:
          'Si se elimina, no podrs recuperarla. Continuamos con esta accin?',
        onConfirmLabel: 'S, eliminar',
        onCancelLabel: 'Cancelar',
        onConfirm: deleteSlide(i),
      });
    },
    [deleteSlide],
  );

  const handleErrorChange = useCallback(
    (newValue: boolean) => setError(newValue),
    [],
  );

  return (
    <div className={styles.containerFluid}>
      {slides.length > 0 && (
        <div className={styles.containerRow}>
          <div className={styles.containerCol6}>
            <Slider
              slides={slides}
              readOnly={readOnly}
              value={value}
              index={index}
              deleteImage={onDeleteSlide}
            />
          </div>
          <div className={styles.containerCol4}>
            <div className={styles.warning}>
              {error && <WarningIcon />}
              <div>
                <span
                  className={[
                    styles.warningText,
                    error && styles.errorText,
                  ].join(' ')}
                >
                  Tipo de archivo: PNG, JPG, JPEG.
                </span>
                <br />
                <span
                  className={[
                    styles.warningText,
                    error && styles.errorText,
                  ].join(' ')}
                >
                  Tamao mximo: 5 MB.
                </span>
              </div>
            </div>
            <div className={styles.dependingCol}>
              <ImageGrid
                slides={slides}
                readOnly={readOnly}
                onChange={onChange}
                placeholder={placeholder}
                value={value}
                index={index}
                deleteImage={onDeleteSlide}
                setError={handleErrorChange}
              />
            </div>
          </div>
        </div>
      )}
      {slides.length === 0 && (
        <EmptyStatus
          helperText={helperText}
          t={t}
          value={value}
          readOnly={readOnly}
        />
      )}
    </div>
  );
};

export default MultiImageInput;
