import React, { useState, useCallback } from 'react';
import { TabsProps, TabProp, TabContentProp } from './types';
import styles from './styles.module.scss';

const Tab: React.FC<TabProp> = ({
  index,
  onTabClick,
  children,
  actualIndex,
  activeClass,
}) => {
  const handleTabChange = useCallback(() => {
    onTabClick(index);
  }, [onTabClick, index]);
  const childArray = React.Children.toArray(children);
  const child = React.isValidElement(childArray[0]) && childArray[0];
  const className = [
    child.props.className,
    actualIndex === index && activeClass,
  ].join(' ');
  return (
    <div
      role="presentation"
      onClick={handleTabChange}
      data-testid="tab-test-id"
    >
      {React.cloneElement(child, { className })}
    </div>
  );
};

const TabContent: React.FC<TabContentProp> = ({
  index,
  activeIndex,
  children,
}) =>
  index === activeIndex ? (
    <div data-testid="tab-content-test">{children}</div>
  ) : null;

/**
 * Tabs currently used in Retail App for My Orders section .
 *
 * Usage:
 * ```
 * <Tabs
 *  initialIndex=
 *  index=
 *  onChange= shows TabContent internal component
 *  activeClass= when the element looks orange, it means ti's the asset.
 * />
 * ```
 */
const Tabs: React.FC<TabsProps> = (props) => {
  const {
    initialIndex = 0,
    children,
    activeClass,
    testId,
    orientation = 'horizontal',
    onChange
  } = props;
  const [index, setIndex] = useState(initialIndex);
  const handleTabChange = useCallback(
    (newIndex: number) => {
      setIndex((oldIndex) => {
        if (onChange) {
          onChange(oldIndex, newIndex);
        }
        return newIndex;
      });
    },
    [onChange],
  );
  const positions = React.Children.map(children, (child) => child);
  const tabs = React.Children.map(positions[0], (child) => {
    if (React.isValidElement(child)) {
      return child.props.children;
    }
    return child;
  });
  const content = React.Children.map(positions[1], (child) => {
    if (React.isValidElement(child)) {
      return child.props.children;
    }
    return child;
  });

  return (
    <div
      className={
        orientation === 'horizontal' ? styles.tabs : styles.tabsVertical
      }
      data-testid={testId}
    >
      <div
        className={[
          styles.tabSection,
          React.isValidElement(positions[0]) && positions[0].props.className,
        ].join(' ')}
      >
        {tabs.map((tab, i) => (
          <Tab
            actualIndex={index}
            key={tab.key}
            onTabClick={handleTabChange}
            index={i}
            activeClass={activeClass}
          >
            {tab}
          </Tab>
        ))}
      </div>
      <div className={styles.contentSection} data-testid="tab-content-test-id">
        {content.map((cont, i) => (
          <TabContent key={cont.key} activeIndex={index} index={i}>
            {cont}
          </TabContent>
        ))}
      </div>
    </div>
  );
};

export default Tabs;
