import { createContext, FC, useEffect } from 'react';
import { ThemeHandlerProps } from './types';

/**
 * This React Context contains the value of the actual theme in the App
 */
export const ThemeContext = createContext('');

/**
 * The ThemeProvider has to wrap the whole app in order to be able to pass the theme variable through React Context API
 * (You must use ``ThemeContext`` in order to get the value for more info https://reactjs.org/docs/context.html).
 * Additional the ThemeProvider add the theme name to class property of the <html> tag in order to use it as a parent style instead of use it in javascript
 * if the prop ``useHtmlClassName``is set to true.
 */
export const ThemeProvider: FC<ThemeHandlerProps> = ({
  children,
  theme,
  useHtmlClassName,
}) => {
  useEffect(() => {
    if (useHtmlClassName) {
      document.getElementsByTagName('html')[0].className = theme;
    }
  }, [theme, useHtmlClassName]);
  return (
    <ThemeContext.Provider value={theme}>{children}</ThemeContext.Provider>
  );
};

export enum Themes {
  default = 'theme-default',
  dark = 'theme-dark',
}
