.input-container {\n  position: relative;\n  text-align: left;\n  width: 100%;\n  z-index: 2;\n\n  textarea {\n    background: transparent;\n    border: none;\n    border-bottom: 1px solid;\n    font-family: Mulish, sans-serif;\n    font-size: 14px;\n    line-height: 17.57px;\n    padding-bottom: 4px;\n    padding-right: 20px;\n    box-sizing: border-box;\n\n    overflow: hidden;\n    resize: none;\n    width: 100%;\n    @include theme-selector('border-color', 'switch-grey', true);\n    @include theme-selector('color', 'color-font', true);\n  }\n  textarea:focus {\n    outline: none;\n\n    &:not([disabled]):not([readonly]) {\n      ~ .label-up2 {\n        font-size: 12px;\n        top: -17px;\n        z-index: 2;\n      }\n    }\n  }\n  textarea:disabled {\n    border-bottom: 0;\n    border-bottom: none;\n    height: inherit;\n    @include theme-selector('color', 'switch-label-grey', true);\n  }\n\n  textarea:read-only {\n    border-bottom: 0;\n    border-bottom: none;\n    height: inherit;\n    padding-bottom: 4px;\n  }\n\n  .label {\n    font-size: 14px;\n    font-weight: 400;\n    left: 0;\n    line-height: 17.57px;\n    position: absolute;\n    top: 0;\n    transition: all 0.17s;\n    z-index: 2;\n    @include theme-selector('color', 'switch-grey', true);\n  }\n\n  textarea:disabled {\n    border-bottom: none;\n    border-bottom: 0;\n\n    ~ .label-up,\n    ~ .label {\n      @include theme-selector('color', 'switch-label-grey', true);\n    }\n  }\n\n  textarea:not([value='']) {\n    ~ .label-up {\n      font-size: 12px;\n      line-height: 15px;\n      top: -17px;\n    }\n  }\n\n  .input-error {\n    animation: errorBounce 250ms linear;\n    font-weight: 200;\n    @include theme-selector('border-color', 'color-red', true);\n\n    ~ .label {\n      animation: errorBounce 250ms linear;\n      font-weight: 200;\n      @include theme-selector('color', 'color-red', true);\n    }\n  }\n\n  .input-error-light {\n    animation: errorBounce 250ms linear;\n    font-weight: 200;\n    @include theme-selector('border-color', 'color-white', true);\n    @include theme-selector('color', 'color-white', true);\n  }\n\n  .input-error::placeholder {\n    @include theme-selector('color', 'color-red', true);\n  }\n  svg {\n    top: 0;\n    height: 20px;\n    position: absolute;\n    right: 0;\n  }\n\n  .input-error-login::placeholder {\n    @include theme-selector('color', 'color-white', true);\n  }\n\n  .input-light {\n    background: transparent;\n    border: none;\n    border-bottom: 1px solid;\n    font-family: Mulish, sans-serif;\n    font-size: 0.95rem;\n    padding-bottom: 4px;\n    width: 100%;\n    @include theme-selector('border-color', 'color-white', true);\n    @include theme-selector('color', 'color-white', true);\n  }\n\n  .input-light::placeholder {\n    font-size: 0.95rem;\n    opacity: 1;\n    @include theme-selector('color', 'color-white', true);\n  }\n\n  input:not([value='']) {\n    ~ .label-up {\n      font-size: 12px;\n      line-height: 15px;\n      top: -17px;\n    }\n  }\n}\n\n.clean-icon {\n  cursor: pointer;\n  height: 20px;\n}\n\n.error-message {\n  font-size: 12px;\n  font-weight: 200;\n  @include theme-selector('color', 'color-red', true);\n}\n\n.error-message-light {\n  font-size: 0.9rem;\n  font-weight: 200;\n  /*  line-height: 30px; */\n  @include theme-selector('color', 'color-white', true);\n}\n\n@keyframes errorBounce {\n  0% {\n    transform: translateX(5px);\n  }\n  25% {\n    transform: translateX(-5px);\n  }\n  50% {\n    transform: translateX(5px);\n  }\n  75% {\n    transform: translateX(-5px);\n  }\n  100% {\n    transform: translateX(5px);\n  }\n}\n","$themes: (\n  default: (\n    color-modal-grey: #6d6e70,\n    color-background: #fafafa,\n    color-real-black: #000000,\n    color-budget-grey: #666666,\n    color-pinkish-grey: #cccccc,\n    color-light-grey: #e9e9e9,\n    color-green: #63e8a9,\n    color-green-switch: #3fd78d,\n    color-green-loading: #6ddaa7,\n    color-white: #ffffff,\n    color-red: #ff1515,\n    color-black: #333333,\n    color-black-degrade: #00000040,\n    color-modal-background: #00000087,\n    color-orange: #ff6c0e,\n    color-faq-orange: #ff6c0d,\n    color-orange-dark: #db5e0e,\n    color-font: #4d4d4d,\n    color-font-2: #2d2d2d,\n    color-title: #101820,\n    color-pearl: #95e1bf,\n    color-light-blue: #00b2e3,\n    color-light-blue-alt: #e9f1f4,\n    color-header-table: #f7f7f7,\n    color-loader: #ff9e7d,\n    color-cerulean: #1aa7e8,\n    badge-green: #d9ffec,\n    badge-red: #fff1f0,\n    badge-blue: #e9f1f4,\n    badge-grey: #f9f9f9,\n    badge-orange: #fff6e9,\n    sla-border: #e5e5e5,\n    card-red: #ff8672,\n    card-green: #96e1c0,\n    card-purple: #bcb6ff,\n    card-orange: #ffac76,\n    switch-green: #04ce6d,\n    switch-grey: #808080,\n    switch-green-disabled: #7ae1b0,\n    switch-grey-disabled: #b5b5b5,\n    switch-toggle-green: #e6faf0,\n    switch-toggle-grey: #f0f0f0,\n    switch-label-grey: #b8b8b8,\n    color-modal-success: #04ce6d,\n    color-modal-warning: #ffa825,\n    color-modal-error: #ff1515,\n    color-modal-info: #338af3,\n    color-gray-hover: #dddddd,\n    color-header-table2: #f5f5f5,\n  ),\n  dark: (),\n);\n\n@mixin theme-selector($key, $color, $isModule) {\n  @each $theme-name, $theme-color in $themes {\n    @if $isModule {\n      #{':global'}(.theme-#{$theme-name}) & {\n        #{$key}: map-get(map-get($themes, $theme-name), $color);\n      }\n    } @else {\n      .theme-#{$theme-name} & {\n        #{$key}: map-get(map-get($themes, $theme-name), $color);\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
