import React, { useState, useCallback, useRef, useEffect } from 'react';
import { Subject, Observable, from } from 'rxjs';
import { debounceTime, switchMap, takeUntil, skip } from 'rxjs/operators';
import { AxiosResponse } from 'axios';
import { AsyncAutoCompleteProps } from './types';
import styles from './styles.module.scss';
import search from '../assets/lupa.svg';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';

import DropDown from '../SharedDropdown/DropDown/DropDown';
import loader from '../assets/Loadercircular.svg';

import { ID } from '../utils/arrays';

const AsyncAutoComplete: React.FC<AsyncAutoCompleteProps> = (props) => {
  const {
    input,
    placeholder,
    disabled = false,
    readOnly,
    labeled,
    getOptions,
    idProperty = 'id',
    showProperty = 'name',
    classes = [],
    onChange,
    getFullObject,
    testId,
  } = props;

  // ---------------------------------------------------------------------------------------------
  const autoComplete = (time: number, selector: any) => (source$: any) =>
    source$.pipe(
      debounceTime(time),
      switchMap((...args: any[]) =>
        from(selector(...args)).pipe(takeUntil(source$.pipe(skip(1)))),
      ),
    );

  const term$: React.Ref<Subject<string>> = useRef(new Subject());
  const results$: React.MutableRefObject<
    Observable<AxiosResponse<any>>
  > = useRef(
    term$.current.pipe(autoComplete(1000, (term: string) => getOptions(term))),
  );

  // ------------------------------------------------------------------------------------------------
  const inputRef: React.Ref<HTMLInputElement> = useRef(null);
  const [options, setOptions] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [generatedId] = useState(ID());
  const [value, setValue] = useState('');
  const [isOpen, setIsOpen] = useState(false);
  const [highlightedIndex, setHighlightedIndex] = useState({
    index: 0,
    id: '',
  });

  const labelClasses = [
    labeled && styles.labelUp,
    ((input && input.value) || value) && !labeled && styles.labelNone,
    styles.label,
  ].join(' ');

  useEffect(() => {
    const subscription = results$.current.subscribe((result) => {
      setOptions(result.data.result);
      setIsLoading(false);
      setIsOpen(true);
    });
    return () => subscription.unsubscribe();
  }, [getOptions]);

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const newValue = event.target.value;
      setValue(newValue);
      if (newValue !== '') {
        setIsLoading(true);
        term$.current.next(newValue);
      } else {
        setOptions([]);
      }
    },
    [],
  );

  const onBlur = useCallback(() => {
    setIsOpen(false);
  }, []);

  const onSelectOption = useCallback(
    (id: number | string) => {
      const option = options.find((op) => op[idProperty] === id);
      input.onChange(id);
      if (option) {
        setValue(option[showProperty]);
      } else {
        setValue('');
      }
      if (onChange) {
        if (getFullObject) {
          onChange(option);
        } else {
          onChange(id);
        }
      }
    },
    [options, input, onChange, idProperty, showProperty, getFullObject],
  );

  const expand = useCallback(() => {
    inputRef.current.focus();
  }, []);

  const deselect = useCallback(() => {
    input.onChange('');
    setValue('');
    if (onChange) {
      onChange('');
    }
  }, [input, onChange]);

  const onFocus = useCallback(() => {
    if (value === '') {
      setOptions([]);
    }
  }, [value]);

  return (
    <div
      id="async-autocomplete-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
    >
      <input
        ref={inputRef}
        id={generatedId}
        {...input}
        value={value}
        onChange={handleChange}
        onFocus={onFocus}
        onBlur={onBlur}
        autoComplete="off"
        readOnly={readOnly}
        disabled={disabled}
      />
      <span className={labelClasses}>{placeholder}</span>
      {input.value !== '' ? (
        <CloseIcon onClick={deselect} className={styles.close} />
      ) : (
        <img
          role="presentation"
          onClick={expand}
          className={styles.expand}
          src={isLoading ? loader : search}
          alt="Logo"
        />
      )}
      <DropDown
        target={generatedId}
        options={options}
        value={value}
        isOpen={isOpen}
        idProperty={idProperty}
        showProperty={showProperty}
        onSelectOption={onSelectOption}
        highlightedIndex={highlightedIndex}
        setHighlightedIndex={setHighlightedIndex}
        testId="autocomplete-test"
      />
    </div>
  );
};

export default AsyncAutoComplete;
