import React, { useCallback } from 'react';
import { TimePickerProps } from './types';
import styles from './styles.module.scss';

const TimePicker: React.FC<TimePickerProps> = (props) => {
  const {
    input,
    meta,
    placeholder,
    labeled,
    onChange,
    value,
    icon: Icon,
    classes = [],
    readOnly,
    testId,
    t,
  } = props;

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      if (input) {
        input.onChange(event.target.value);
      }
      if (onChange) {
        onChange(event.target.value);
      }
    },
    [input, onChange],
  );

  const hasError = meta && meta.invalid && meta.touched;
  const inputClasses = [hasError && styles.inputError].join(' ');

  const errorData = meta && meta.error && meta.error.split(':');

  return (
    <div
      id="numeric-input-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
    >
      {input.value && labeled && (
        <span className={styles.label}>{placeholder}</span>
      )}
      <input
        {...input}
        value={input ? input.value : value}
        readOnly={readOnly}
        placeholder={placeholder}
        onChange={handleChange}
        className={inputClasses}
        data-testid="input-test"
      />
      {hasError && (
        <span className={styles.errorMessage} data-testid="span-test">
          {t(`Validations:${errorData[0]}`, { data: errorData[1] })}
        </span>
      )}
      {Icon && <Icon />}
    </div>
  );
};

export default TimePicker;
