import React from 'react';
import styles from './styles.module.scss';
import { ReactComponent as PuffIcon } from '../assets/puff.svg';
import { InnerLoaderProps } from './types';

const InnerLoader: React.FC<InnerLoaderProps> = ({
  innerLoaderColor,
  testId,
}) => (
  <div
    className={
      innerLoaderColor === 'orange' ? styles.loaderOrange : styles.loader
    }
    data-testid={testId}
  >
    <PuffIcon />
  </div>
);

export default InnerLoader;
