import React, { useCallback, useEffect, useState } from 'react';
import { Subject } from 'rxjs';
import {
  AlertsProps,
  AlertData,
  SUCCESS_ALERT,
  INFO_ALERT,
  WARNING_ALERT,
  AlertButtonColors,
  USER_ALERT,
  ERROR_ALERT,
  CONFIG_ALERT,
} from './types';
import Modal from '../Modal/Modal';
import styles from './styles.module.scss';
import { ReactComponent as SuccessIcon } from '../assets/ico-ok.svg';
import { ReactComponent as WarningIcon } from '../assets/ico-warning.svg';
import { ReactComponent as UserIcon } from '../assets/ico-user-alert.svg';
import { ReactComponent as ErrorIcon } from '../assets/ico-error.svg';
import { ReactComponent as InfoIcon } from '../assets/ico-info.svg';
import { ReactComponent as ConfigIcon } from '../assets/ico-config.svg';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';
import SimpleButton from '../SimpleButton/SimpleButton';

export const $alerts = new Subject<AlertData>();

const getAlertTheme = (alerts: AlertData, onClose: () => void) => {
  const { type, onCancel, onCancelLabel, onConfirm, onConfirmLabel } = alerts;

  const confirmAction = () => () => {
    if (onConfirm) {
      onConfirm();
    }
    onClose();
  };

  const cancelAction = () => () => {
    if (onCancel) {
      onCancel();
    }
    onClose();
  };

  const renderActions = (buttonColor: AlertButtonColors) => (
    <>
      {onCancelLabel && (
        <SimpleButton
          label={alerts.onCancelLabel}
          buttonColor="white"
          type="button"
          onClick={cancelAction()}
        />
      )}
      {onConfirmLabel && (
        <SimpleButton
          label={alerts.onConfirmLabel}
          buttonColor={buttonColor}
          type="button"
          onClick={confirmAction()}
        />
      )}
    </>
  );

  switch (type) {
    case SUCCESS_ALERT:
      return {
        header: <SuccessIcon />,
        actions: renderActions(SUCCESS_ALERT),
      };
    case WARNING_ALERT:
      return {
        header: <WarningIcon />,
        actions: renderActions(WARNING_ALERT),
      };
    case USER_ALERT:
      return {
        header: <UserIcon />,
        actions: renderActions(ERROR_ALERT),
      };
    case INFO_ALERT:
      return {
        header: <InfoIcon />,
        actions: renderActions(INFO_ALERT),
      };
    case ERROR_ALERT:
      return {
        header: <ErrorIcon />,
        actions: renderActions(ERROR_ALERT),
      };
    case CONFIG_ALERT:
      return {
        header: <ConfigIcon />,
        actions: renderActions(INFO_ALERT),
      };
    default:
      return {
        header: '',
        actions: '',
      };
  }
};

const Alerts: React.FC<AlertsProps> = () => {
  const [alerts, setAlert] = useState<AlertData>({
    type: null,
    title: '',
  });

  const onClose = useCallback(() => {
    $alerts.next({
      type: null,
      title: '',
    });
    if (alerts.onClose) {
      alerts.onClose();
    }
  }, [alerts]);

  const alertTheme = getAlertTheme(alerts, onClose);

  useEffect(() => {
    const subscription = $alerts.subscribe((alertData) => {
      setAlert(alertData);
    });
    return () => subscription.unsubscribe();
  }, []);

  useEffect(() => {
    document.onkeydown = (event) => {
      if (event.keyCode === 13 && alerts.type !== null) {
        event.preventDefault();
        event.stopPropagation();
        onClose();
      }
    };
  }, [alerts, onClose]);

  return (
    <Modal
      isOpen={alerts.type !== null}
      width="560px"
      height="auto"
      onClose={onClose}
      closeOnBlur={false}
    >
      <div
        id="alerts-id"
        className={styles.alertContainer}
        data-testid="alerts-test"
      >
        <CloseIcon onClick={onClose} className={styles.alertClose} />
        <div className={styles.alertTop}>{alertTheme.header}</div>
        <div className={styles.alertBottom}>
          <div className={styles.alertTitle}>{alerts.title}</div>
          <div className={styles.alertBody}>{alerts.subMessage}</div>
          <div className={styles.alertActions}>{alertTheme.actions}</div>
        </div>
      </div>
    </Modal>
  );
};

export default Alerts;
