import React, { useCallback, useState } from 'react';
import { NumericInputProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';
import { ReactComponent as IconInputError } from '../assets/ico-input-error.svg';

const MAX_CHARACTER = 15;

const NumericInput: React.FC<NumericInputProps> = (props) => {
  const {
    input,
    meta,
    placeholder,
    labeled,
    onChange,
    value,
    icon: Icon,
    classes = [],
    negative,
    readOnly,
    testId,
    disabled,
    t,
  } = props;
  const [internalValue, setInternalValue] = useState({
    internalValue: input ? input.value : value,
    isActive: false,
  });

  const handleNumberValidation = useCallback(
    (valueInput: string) => {
      let emitValue;
      if (
        /^\d+\.?\d*$/.test(valueInput) &&
        valueInput.length <= MAX_CHARACTER
      ) {
        emitValue = negative
          ? Math.abs(parseFloat(valueInput))
          : parseFloat(valueInput);
      } else if (
        /^\d+,?\d*$/.test(valueInput) &&
        valueInput.length <= MAX_CHARACTER
      ) {
        const noComaValue = valueInput.replace(',', '.');
        emitValue = negative
          ? Math.abs(parseFloat(noComaValue))
          : parseFloat(noComaValue);
      }
      return emitValue || emitValue === 0 ? emitValue : '';
    },
    [negative],
  );

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const checkedValue = handleNumberValidation(event.target.value);
      if (event.target.value === '' || typeof checkedValue === 'number') {
        setInternalValue({
          internalValue: event.target.value,
          isActive: true,
        });
        if (input) {
          input.onChange(checkedValue);
        }
        if (onChange) {
          onChange(checkedValue, input && input.name);
        }
      }
    },
    [input, onChange, handleNumberValidation],
  );

  const handleBlur = useCallback(() => {
    setInternalValue({
      internalValue: input ? input.value : value,
      isActive: false,
    });
    if (input) {
      input.onBlur();
    }
  }, [input, value]);

  const handleFocus = useCallback(() => {
    setInternalValue({
      internalValue: input ? input.value : value,
      isActive: true,
    });
  }, [input, value]);

  const labelClasses = [
    labeled && styles.labelUp,
    ((input && input.value) || value) && !labeled && styles.labelNone,
    styles.label,
  ].join(' ');

  const hasError = meta && meta.invalid && meta.touched;
  const inputClasses = [hasError && styles.inputError].join(' ');
  const validValue = input ? input.value : value;
  const variableToShow = internalValue.isActive
    ? internalValue.internalValue.toString()
    : validValue;

  const errorData = meta && meta.error && meta.error.split(':');

  const cleanValue = useCallback(() => {
    input.onChange('');
    if (onChange) {
      onChange('');
    }
  }, [input, onChange]);

  const showIcon = useCallback(() => {
    if (disabled || readOnly) {
      return null;
    }

    if (Icon) {
      return <Icon />;
    }

    if (hasError) {
      return <IconInputError className={styles.cleanIcon} />;
    }

    if ((input && input.value) || value) {
      return <CloseIcon onClick={cleanValue} className={styles.cleanIcon} />;
    }

    return null;
  }, [Icon, cleanValue, input, value, hasError, disabled, readOnly]);

  return (
    <div
      id="numeric-input-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
    >
      <input
        {...input}
        readOnly={readOnly}
        value={variableToShow}
        onChange={handleChange}
        className={inputClasses}
        onBlur={handleBlur}
        onFocus={handleFocus}
        data-testid="input-test"
        disabled={disabled}
      />
      <span className={labelClasses}>{placeholder}</span>
      {hasError && (
        <span className={styles.errorMessage}>
          {t(`Validations:${errorData[0]}`, { data: errorData[1] })}
        </span>
      )}
      {showIcon()}
    </div>
  );
};

export default NumericInput;
