import React, { useState, useCallback, useRef, useEffect } from 'react';
import { MultiInputProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as ChevronIcon } from '../assets/chevronA.svg';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';
import DropDown from '../SharedDropdown/DropDown/DropDown';
import { ID, getPropertyByPathString } from '../utils/arrays';

/**
 * @param nameOptionProperty its the name of the value of the frst option that you select
 * @param placeholder
 * @param labeled
 * @param options [{id, name}, ...] by default
 * @param idProperty Name of the property asociated to 'id'. Default: 'id'
 * @param showProperty Name of the property asociated to label to show on each option. Default: 'name'
 * @param onChange
 * @param dropDownOnly If 'true' the behavior is like Select component
 * @param readOnly If 'true' the values cant be modified
 * @param disabled Its a boolean
 *
 *
 */

/**
 *
 * PD: In the next iterations for this component, consider the next refactor:
 *       - The selector of this component is the Autocomplete component that ignore input prop as handler, but,
 *          register a new field to the form
 *
 *
 *
 */
const MultiInput: React.FC<MultiInputProps> = (props) => {
  const {
    input,
    meta,
    value,
    placeholder,
    labeled,
    options = [],
    nameOptionProperty,
    idProperty = 'id',
    showProperty = 'name',
    iconProperty = 'icon',
    classes = [],
    onChangeOption,
    dropDownOnly,
    disabled = false,
    t,
    form,
    testId,
    readOnly,
  } = props;

  const [selectedValue, setSelectedValue] = useState('');
  const selectorRef: React.Ref<HTMLInputElement> = useRef(null);
  const [filteredOptions, setFilteredOptions] = useState([...options]);
  const [generatedId] = useState(ID());
  const [optionValue, setOptionValue] = useState('');
  const [isOpen, setIsOpen] = useState(false);
  const [highlightedIndex, setHighlightedIndex] = useState({
    index: 0,
    id: '',
  });

  const checkSelectorValue = useCallback((state) => {
    setSelectedValue(state.value);
  }, []);

  useEffect(() => {
    const selectorSubscriber = form.registerField(
      nameOptionProperty,
      checkSelectorValue,
      { value: true },
    );

    return () => {
      selectorSubscriber();
    };
  }, [nameOptionProperty, checkSelectorValue]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const newValue = event.target.value;
      setOptionValue(newValue);
      if (options.length > 0) {
        if (newValue) {
          const newOptions = options.filter((option) =>
            option[showProperty].toLowerCase().includes(newValue),
          );
          setFilteredOptions(newOptions);
          setHighlightedIndex({
            index: 0,
            id:
              newOptions[0] &&
              getPropertyByPathString(newOptions[0], idProperty),
          });
        } else {
          setFilteredOptions(options);
        }
      }
    },
    [options, idProperty, showProperty],
  );

  const onBlur = useCallback(() => {
    const selectedOption = options.find(
      (option) =>
        form.getFieldState(nameOptionProperty)?.value ===
        getPropertyByPathString(option, idProperty),
    );
    if (selectedOption) {
      setOptionValue(selectedOption[showProperty]);
    } else {
      setOptionValue('');
      setFilteredOptions(options);
    }
    setIsOpen(false);
    // input.onBlur();
  }, [form, nameOptionProperty, options, idProperty, showProperty]);

  const onSelectOption = useCallback(
    (id: number | string) => {
      // input.onChange(id);
      form.change(nameOptionProperty, id);
      if (onChangeOption) {
        onChangeOption(id);
      }
    },
    [
      // input,
      onChangeOption,
      nameOptionProperty,
      form,
    ],
  );

  const onKeyPress = useCallback(
    (event: React.KeyboardEvent<HTMLInputElement>) => {
      switch (event.keyCode) {
        case 38:
          if (highlightedIndex.index > 0) {
            setHighlightedIndex({
              index: highlightedIndex.index - 1,
              id: highlightedIndex.id,
            });
          }
          break;
        case 40:
          if (highlightedIndex.index < options.length - 1) {
            setHighlightedIndex({
              index: highlightedIndex.index + 1,
              id: highlightedIndex.id,
            });
          }
          break;
        case 13:
          event.preventDefault();
          // input.onChange(
          //   getPropertyByPathString(
          //     filteredOptions[highlightedIndex.index],
          //     idProperty,
          //   ),
          // );
          onSelectOption(
            getPropertyByPathString(
              filteredOptions[highlightedIndex.index],
              idProperty,
            ),
          );
          setTimeout(() => selectorRef.current.blur()); // Horrible
          break;

        default:
          break;
      }
    },
    [
      highlightedIndex,
      options,
      // input,
      selectorRef,
      filteredOptions,
      idProperty,
      onSelectOption,
    ],
  );

  const expand = useCallback(() => {
    selectorRef.current.focus();
  }, []);

  const deselect = useCallback(() => {
    input.onChange('');
    setOptionValue('');
    onSelectOption('');
  }, [input, onSelectOption]);

  const onFocus = useCallback(() => {
    if (!disabled && !readOnly) {
      setIsOpen(true);
    }
  }, [disabled, readOnly]);

  useEffect(() => {
    setFilteredOptions([...options]);
  }, [options]);

  useEffect(() => {
    const option = filteredOptions.find(
      (op) => op[idProperty] === form.getFieldState(nameOptionProperty)?.value,
    );
    if (option && option[showProperty] !== optionValue) {
      setOptionValue(option[showProperty]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    // input.optionValue,
    showProperty,
    idProperty,
    filteredOptions,
    optionValue,
    nameOptionProperty,
  ]);

  useEffect(() => {
    if (!selectedValue) {
      setOptionValue('');
    }
  }, [selectedValue, setOptionValue]);

  const hasError = meta && meta.invalid && meta.touched;
  const inputClasses = [hasError && styles.inputError].join(' ');

  const labelClasses = [
    labeled && styles.labelUp,
    ((input && input.value) || value) && !labeled && styles.labelNone,
    styles.label,
  ].join(' ');

  const showOptionIcon = useCallback(() => {
    const selectedOption = options.find(
      (option) =>
        form.getFieldState(nameOptionProperty)?.value ===
        getPropertyByPathString(option, idProperty),
    );

    if (selectedOption) {
      const { icon: Icon } = selectedOption;

      if (Icon) {
        return <Icon className={styles.optionIcon} />;
      }
    }

    return <></>;
  }, [options, form, idProperty, nameOptionProperty]);

  const showSelectorHandlerIcon = useCallback(() => {
    const showCloseIcon =
      form.getFieldState(nameOptionProperty)?.value !== '' &&
      form.getFieldState(nameOptionProperty)?.value !== undefined &&
      !dropDownOnly;
    if (disabled || readOnly) {
      return <div />;
    }
    if (showCloseIcon) {
      return <CloseIcon onClick={deselect} className={styles.selectorClose} />;
    }
    return <ChevronIcon onClick={expand} className={styles.expand} />;
  }, [
    // input,
    form,
    nameOptionProperty,
    dropDownOnly,
    disabled,
    deselect,
    expand,
    readOnly,
  ]);

  const showInputHandlerIcon = useCallback(() => {
    if (disabled || readOnly) {
      return <div />;
    }
    if (input.value) {
      return <CloseIcon onClick={deselect} className={styles.inputClose} />;
    }

    return <></>;
  }, [disabled, readOnly, deselect, input.value]);

  return (
    <div
      id="multi-input-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
    >
      <div
        className={[
          styles.inputWrapper,
          selectedValue !== undefined &&
            selectedValue !== '' &&
            styles.inputSelected,
        ].join(' ')}
      >
        <div>
          {showOptionIcon()}
          <input
            ref={selectorRef}
            id={generatedId}
            name={nameOptionProperty}
            value={optionValue}
            onChange={handleChange}
            className={inputClasses}
            onKeyDown={onKeyPress}
            onFocus={onFocus}
            onBlur={onBlur}
            autoComplete="off"
            readOnly={dropDownOnly || readOnly}
            disabled={disabled}
            placeholder={placeholder}
            data-testid="multi-input-autocomplete-test"
          />
          {showSelectorHandlerIcon()}
        </div>
        {selectedValue !== undefined && selectedValue !== '' && (
          <div>
            <input
              {...input}
              value={input ? input.value : value}
              placeholder={placeholder}
              className={inputClasses}
              data-testid="multi-input-input-test"
              readOnly={readOnly}
              disabled={disabled}
            />
            {input.value && labeled && (
              <span className={labelClasses}>{placeholder}</span>
            )}
            {showInputHandlerIcon()}
          </div>
        )}
      </div>

      {hasError && (
        <span className={styles.errorMessage}>
          {t(`Validations:${meta.error}`)}
        </span>
      )}

      <DropDown
        target={generatedId}
        options={filteredOptions}
        value={optionValue}
        isOpen={isOpen}
        idProperty={idProperty}
        showProperty={showProperty}
        iconProperty={iconProperty}
        onSelectOption={onSelectOption}
        highlightedIndex={highlightedIndex}
        setHighlightedIndex={setHighlightedIndex}
        testId="multi-input-dropdown-test"
      />
    </div>
  );
};

export default MultiInput;
