px;\n  font-weight: 700;\n  padding: 12px 50px;\n  position: relative;\n  transition: background-color 200ms ease-in;\n}\n\n.button:disabled {\n  @include theme-selector('background-color', 'color-pinkish-grey', true);\n  @include theme-selector('color', 'color-white', true);\n  border: none;\n  cursor: default !important;\n\n  &.outlined-button {\n    background-color: transparent;\n    @include theme-selector('border-color', 'color-pinkish-grey', true);\n    @include theme-selector('color', 'color-pinkish-grey', true);\n    border: 2px solid;\n  }\n}\n\n.button:focus {\n  outline: none;\n}\n\n.button-container {\n  align-items: center;\n  display: flex;\n  justify-content: space-around;\n  svg {\n    margin-right: 8px;\n  }\n}\n\n.green-button {\n  @include theme-selector('background-color', 'color-green', true);\n  @include theme-selector('color', 'color-white', true);\n  .button-loading {\n    align-items: center;\n    @include theme-selector('background-color', 'color-green', true);\n    border-radius: 37px;\n    display: flex;\n    height: 100%;\n    justify-content: center;\n    left: 0px;\n    position: absolute;\n    top: 0px;\n    transition: clip-path 800ms linear, background-color 600ms linear;\n    width: 100%;\n  }\n}\n\n.orange-button {\n  @include theme-selector('background-color', 'color-orange', true);\n  @include theme-selector('color', 'color-white', true);\n  .button-loading {\n    align-items: center;\n    @include theme-selector('background-color', 'color-orange', true);\n    border-radius: 37px;\n    display: flex;\n    height: 100%;\n    justify-content: center;\n    left: 0px;\n    position: absolute;\n    top: 0px;\n    transition: clip-path 800ms linear, background-color 600ms linear;\n    width: 100%;\n  }\n}\n\n.white-button {\n  @include theme-selector('color', 'color-orange', true);\n  @include theme-selector('background-color', 'color-white', true);\n  z-index: 2;\n  /*   border-radius: 50px;\n  font-weight: 800;\n  margin: 0 auto; */\n  .button-loading {\n    align-items: center;\n    @include theme-selector('background-color', 'color-white', true);\n    border-radius: 37px;\n    display: flex;\n    height: 100%;\n    justify-content: center;\n    left: 0px;\n    position: absolute;\n    top: 0px;\n    transition: clip-path 800ms linear, background-color 600ms linear;\n    width: 100%;\n  }\n}\n\n.black-button {\n  @include theme-selector('background-color', 'color-real-black', true);\n  @include theme-selector('color', 'color-white', true);\n  .button-loading {\n    align-items: center;\n    @include theme-selector('background-color', 'color-real-black', true);\n    border-radius: 37px;\n    display: flex;\n    height: 100%;\n    justify-content: center;\n    left: 0px;\n    position: absolute;\n    top: 0px;\n\n    transition: clip-path 800ms linear, background-color 600ms linear;\n    width: 100%;\n  }\n}\n\n.outlined-button {\n  @include theme-selector('border-color', 'color-orange', true);\n  background: transparent;\n  border: 2px solid;\n  padding: 10px 20px;\n}\n\n.full-width {\n  width: -webkit-fill-available;\n  width: -moz-available;\n}\n\n.action-button {\n  font-size: 16px;\n  font-weight: 700;\n  padding: 13px 50px;\n  width: 189px;\n}\n\n.icon-button {\n  font-size: 14px;\n  font-weight: 700;\n  padding: 6px 21px;\n}\n\n// .button-loading {\n//   align-items: center;\n//   @include theme-selector('background-color', 'color-green-loading', true);\n//   border-radius: 37px;\n//   display: flex;\n//   height: 100%;\n//   justify-content: center;\n//   left: 0px;\n//   position: absolute;\n//   top: 0px;\n//   transition: clip-path 800ms linear, background-color 600ms linear;\n//   width: 100%;\n// }\n\n.failure {\n  @include theme-selector('background-color', 'color-red', true);\n}\n\n.success {\n  @include theme-selector('background-color', 'color-green', true);\n}\n\n.transform-anim {\n  animation: transform-anim 1000ms ease-in-out 800ms,\n    transform-anim 800ms linear forwards reverse 1800ms;\n}\n\n.explosion {\n  height: 100%;\n  left: 0;\n  opacity: 0;\n  position: fixed;\n  top: 0;\n  transition: clip-path 800ms ease-in-out;\n  transition-delay: 1800ms;\n  visibility: hidden;\n  width: 100%;\n  svg {\n    height: 40px;\n    position: fixed;\n    @include theme-selector('stroke', 'color-white', true);\n    stroke-dasharray: 400;\n    stroke-dashoffset: 400;\n    transition: stroke-dashoffset 200ms ease-in;\n    transition-delay: 1800ms;\n    path {\n      stroke-width: 8px;\n    }\n  }\n  will-change: clip-path;\n  z-index: 5;\n}\n\n.explosion-anim {\n  animation: explosion-anim 2000ms ease-in-out forwards 1800ms;\n  svg {\n    stroke-dashoffset: 0;\n  }\n}\n\n@keyframes transform-anim {\n  0% {\n    clip-path: circle(100%);\n  }\n  50% {\n    clip-path: circle(25px);\n    color: transparent;\n  }\n  100% {\n    clip-path: circle(25px);\n    color: transparent;\n  }\n}\n\n@keyframes explosion-anim {\n  0% {\n    opacity: 1;\n    visibility: visible;\n  }\n  50% {\n    opacity: 1;\n    visibility: visible;\n  }\n  100% {\n    opacity: 0;\n    visibility: hidden;\n  }\n}\n","$themes: (\n  default: (\n    color-modal-grey: #6d6e70,\n    color-background: #fafafa,\n    color-real-black: #000000,\n    color-budget-grey: #666666,\n    color-pinkish-grey: #cccccc,\n    color-light-grey: #e9e9e9,\n    color-green: #63e8a9,\n    color-green-switch: #3fd78d,\n    color-green-loading: #6ddaa7,\n    color-white: #ffffff,\n    color-red: #ff1515,\n    color-black: #333333,\n    color-black-degrade: #00000040,\n    color-modal-background: #00000087,\n    color-orange: #ff6c0e,\n    color-faq-orange: #ff6c0d,\n    color-orange-dark: #db5e0e,\n    color-font: #4d4d4d,\n    color-font-2: #2d2d2d,\n    color-title: #101820,\n    color-pearl: #95e1bf,\n    color-light-blue: #00b2e3,\n    color-light-blue-alt: #e9f1f4,\n    color-header-table: #f7f7f7,\n    color-loader: #ff9e7d,\n    color-cerulean: #1aa7e8,\n    badge-green: #d9ffec,\n    badge-red: #fff1f0,\n    badge-blue: #e9f1f4,\n    badge-grey: #f9f9f9,\n    badge-orange: #fff6e9,\n    sla-border: #e5e5e5,\n    card-red: #ff8672,\n    card-green: #96e1c0,\n    card-purple: #bcb6ff,\n    card-orange: #ffac76,\n    switch-green: #04ce6d,\n    switch-grey: #808080,\n    switch-green-disabled: #7ae1b0,\n    switch-grey-disabled: #b5b5b5,\n    switch-toggle-green: #e6faf0,\n    switch-toggle-grey: #f0f0f0,\n    switch-label-grey: #b8b8b8,\n    color-modal-success: #04ce6d,\n    color-modal-warning: #ffa825,\n    color-modal-error: #ff1515,\n    color-modal-info: #338af3,\n    color-gray-hover: #dddddd,\n    color-header-table2: #f5f5f5,\n  ),\n  dark: (),\n);\n\n@mixin theme-selector($key, $color, $isModule) {\n  @each $theme-name, $theme-color in $themes {\n    @if $isModule {\n      #{':global'}(.theme-#{$theme-name}) & {\n        #{$key}: map-get(map-get($themes, $theme-name), $color);\n      }\n    } @else {\n      .theme-#{$theme-name} & {\n        #{$key}: map-get(map-get($themes, $theme-name), $color);\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
