import React, { useCallback } from 'react';
import { PaginatorProps } from './types';
import styles from './styles.module.scss';
import ArrowButton from './components/ArrowButton/ArrowButton';

/**
 *
 * @param currentPage Number of the page currently selected
 * @param totalPages Quantity of pages available for pagination
 * @param onPageClick Method for changing the selected page (page:number)=>void
 * @param dataTestId Id for tests
 *
 */

const Paginator: React.FC<PaginatorProps> = ({
  currentPage,
  totalPages,
  onPageClick,
  dataTestId,
}) => {
  const getPagination = useCallback(() => {
    const res = [1];

    const page = currentPage + 1;
    let beforePage = page - 1;
    let afterPage = page + 1;

    if (page > 4 && totalPages >= 9) {
      res.push(-1);
    }

    beforePage = page - 2;
    if (page > totalPages - 5) {
      beforePage = totalPages - 6;
    }
    if (beforePage <= 0) {
      beforePage = 2;
    }

    afterPage = page + 2;
    if (page < 5) {
      afterPage = 7;
    }
    if (afterPage >= totalPages) {
      afterPage = totalPages - 1;
    }

    for (let plength = beforePage; plength <= afterPage; plength++) {
      if (plength > totalPages || plength <= 1) {
        continue;
      }
      res.push(plength);
    }

    if (afterPage < totalPages - 1) {
      res.push(-1);
    }

    if (totalPages > 1) {
      res.push(totalPages);
    }

    return res;
  }, [currentPage, totalPages]);

  const onClick = useCallback(
    (page: number) => () => {
      onPageClick(page);
    },
    [onPageClick],
  );

  return (
    <div data-testid={dataTestId} className={styles.container}>
      <div className={styles.paginator}>
        <ArrowButton
          onClick={onClick(currentPage - 1)}
          disabled={currentPage === 0}
        />
        {getPagination().map((x, idx) => (
          <div className={styles.pageButtonContainer} key={idx}>
            {x === -1 && <div className={styles.dots}>...</div>}
            {x !== -1 && (
              <div
                className={`${styles.pageButton} ${
                  x - 1 === currentPage && styles.selected
                }`}
                onClick={onClick(x - 1)}
                role="presentation"
              >
                {x}
              </div>
            )}
          </div>
        ))}
        <ArrowButton
          direction="right"
          onClick={onClick(currentPage + 1)}
          disabled={currentPage >= totalPages - 1}
        />
      </div>
    </div>
  );
};

export default Paginator;
