import React, { useCallback } from 'react';
import { DaysProps } from './types';
import styles from './styles.module.scss';

const Day: React.FC<DaysProps> = (props) => {
  const {
    day,
    onDayClick,
    onHoverDay,
    isFrom,
    isFromInterval,
    isTo,
    isEqual,
    available,
  } = props;
  const handleClick = useCallback(() => {
    if (available) {
      onDayClick(day);
    }
  }, [available, day, onDayClick]);
  const handleHover = useCallback(() => onHoverDay(day), [day, onHoverDay]);
  const classes = [
    isFrom && styles.from,
    isTo && styles.to,
    isFromInterval && styles.isFromInterval,
    isEqual && styles.isEqual,
    !available && styles.blocked,
  ].join(' ');

  return (
    <span
      about="day"
      className={classes}
      role="presentation"
      onMouseDown={handleClick}
      onMouseEnter={handleHover}
    >
      {day}
    </span>
  );
};

export default Day;
