import React, { useCallback, useRef, useState } from 'react';
import { TopBarProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as LogoPickit } from '../assets/pickit-white-logo.svg';
import { ReactComponent as UserIcon } from '../assets/user-orange.svg';
import { ReactComponent as LogoutIcon } from '../assets/logout.svg';
import { ReactComponent as UserDropdown } from '../assets/avatar.svg';
import AppMobileModal from '../AppMobileModal/AppMobileModal';
import { Flags, PickitApps } from '../constants/pickit';
import { useDetectOutsideClick } from '../DetectOutSideClickHook/DetectOutSideClickHook';

const TopBar: React.FC<TopBarProps> = (props) => {
  const {
    profile,
    appName,
    flag,
    fullName,
    handleLogout,
    testId,
    goIndex,
  } = props;
  const dropdownRef = useRef(null);
  const [isActive, setIsActive] = useDetectOutsideClick(dropdownRef, false);

  const [modal, setModalOpen] = useState({
    open: false,
    content: '',
    action: '',
    notificationId: null,
  });
  const closeModal = useCallback(() => {
    setModalOpen({ ...modal, open: false });
  }, [modal]);

  const onClick = useCallback(() => setIsActive(!isActive), [
    setIsActive,
    isActive,
  ]);

  return (
    <div data-testid={testId} id="to-bar-id" className={styles.topBar}>
      {PickitApps[appName] === PickitApps.point && (
        <AppMobileModal
          isOpen={modal.open}
          onClose={closeModal}
          content={modal.content}
        />
      )}
      <div className={styles.leftSide}>
        <div className={styles.logo} data-testid="logo-index">
          <LogoPickit
            role="presentation"
            onClick={goIndex}
            data-testid="img-test"
          />
        </div>
      </div>
      <div className={styles.rightSide} data-testid="right-side-test">
        {profile ? (
          <>
            <div className={styles.appName}>
              <span>{PickitApps[appName]}</span>
            </div>
            <div className={styles.flag}>
              <img src={Flags[flag]} alt="LOGO" />
            </div>
            <div
              className={styles.topbarUserDropdown}
              onClick={onClick}
              role="presentation"
              ref={dropdownRef}
              data-testid="user-dropdown-test"
            >
              <UserIcon className={styles.userIcons} />
              {isActive && (
                <>
                  <div
                    className={styles.userDropdownContainer}
                    data-testid="dropdown-container-test"
                  >
                    <div className={styles.userDropdownItem}>
                      <span>{fullName}</span>
                      <UserDropdown />
                    </div>
                    <div
                      className={styles.userDropdownItem}
                      role="presentation"
                      onClick={handleLogout}
                      data-testid="user-test"
                    >
                      <span>Cerrar sesin</span>
                      <LogoutIcon />
                    </div>
                  </div>
                </>
              )}
            </div>
          </>
        ) : (
          <>
            <div className={styles.flagLoader} />
            <div className={styles.rightBarLoader} />
          </>
        )}
      </div>
    </div>
  );
};

export default TopBar;
