import React, { useState, useCallback, useEffect, useRef } from 'react';
import { createPortal } from 'react-dom';
import moment from 'moment';
import { DateOverlayProps } from './types';
import { ReactComponent as Chevron } from '../../../assets/chevronA.svg';
import styles from './styles.module.scss';
import Day from '../Day/Day';
import { disableScroll, enableScroll } from '../../../utils/scroll';

const overlayRoot = document.getElementById('overlay-root');

const DateOverlay: React.FC<DateOverlayProps> = (props) => {
  const {
    target,
    isOpen,
    makeDays,
    dayGrid,
    goBack,
    goFront,
    displayDate,
    momentInstance,
    onSelection,
    singleSelection,
    setIsOpen,
  } = props;
  const [parentRect, setParentRect] = useState({} as DOMRect);
  const [element] = useState(document.createElement('div'));
  const divRef: React.Ref<HTMLDivElement> = useRef(null);
  const [selectionState, setSelectionState] = useState({
    selectionActive: false,
    from: null,
    to: null,
  });

  useEffect(() => {
    if (isOpen) {
      disableScroll();
    } else {
      enableScroll();
    }

    return () => {
      enableScroll();
    };
  }, [isOpen]);

  const handleClose = useCallback(
    (event) => {
      if (
        !divRef.current.contains(event.target) &&
        event.target.getAttribute('about') !== 'day'
      ) {
        setIsOpen(false);
      }
    },
    [setIsOpen],
  );
  useEffect(() => {
    if (isOpen) {
      document.addEventListener('mousedown', handleClose);
    } else {
      document.removeEventListener('mousedown', handleClose);
      setSelectionState({
        selectionActive: false,
        from: null,
        to: null,
      });
    }
    return () => document.removeEventListener('mousedown', handleClose);
  }, [handleClose, isOpen]);

  useEffect(() => {
    if (isOpen) {
      const parent = document.getElementById(target);
      const newRect: DOMRect = parent.getBoundingClientRect();
      setParentRect(newRect);
    }
  }, [isOpen, target]);

  useEffect(() => {
    if (!overlayRoot) {
      return () => {};
    }

    const parent = document.getElementById(target);

    if (parent) {
      const newRect: DOMRect = parent.getBoundingClientRect();
      if (parentRect.left !== newRect.left) {
        setParentRect(newRect);
      }
      overlayRoot.appendChild(element);
    }

    return () => {
      if (overlayRoot.contains(element)) {
        overlayRoot.removeChild(element);
      }
    };
  }, [element, target, parentRect]);

  const dayClick = useCallback(
    (day: number) => {
      if (selectionState.selectionActive) {
        onSelection({
          from: selectionState.from.format('DD/MM/YYYY'),
          to: selectionState.to.format('DD/MM/YYYY'),
        });
        setSelectionState({
          ...selectionState,
          selectionActive: false,
        });
      } else if (singleSelection) {
        onSelection({
          from: momentInstance
            .clone()
            .startOf('month')
            .add(day - 1, 'days')
            .format('DD/MM/YYYY'),
        });
      } else {
        const actualDay = momentInstance
          .clone()
          .startOf('month')
          .add(day - 1, 'days');
        setSelectionState({
          ...selectionState,
          from: actualDay,
          to: actualDay,
          selectionActive: true,
        });
      }
    },
    [selectionState, momentInstance, onSelection, singleSelection],
  );

  const onHover = useCallback(
    (day: number) => {
      const actualMonth = momentInstance.clone();
      const hoverDay = actualMonth
        .clone()
        .startOf('month')
        .add(day - 1, 'days');
      const isAfter =
        selectionState.from && hoverDay.isAfter(selectionState.from);
      const isSameFrom =
        selectionState.from &&
        selectionState.from.isSame(
          moment()
            .startOf('month')
            .add(day - 1, 'days'),
        );
      if (selectionState.selectionActive && (isAfter || isSameFrom)) {
        setSelectionState({
          ...selectionState,
          to: hoverDay,
        });
      }
    },
    [selectionState, momentInstance],
  );

  const overlayClasses = [styles.datePicker, isOpen && styles.show].join(' ');

  const overlayStructure = useCallback(() => {
    const top = parentRect.top + parentRect.height;
    const positionCurr =
      window.innerHeight - parentRect?.bottom <
      divRef.current?.clientHeight + parentRect?.height
        ? divRef.current?.clientHeight + parentRect.height
        : 0;

    if (Number.isNaN(top)) {
      return null;
    }

    return (
      <div
        ref={divRef}
        className={overlayClasses}
        style={{
          left: parentRect.left,
          top: top - positionCurr,
          width: parentRect.width,
        }}
      >
        <div className={styles.header}>
          <div role="presentation" onClick={goBack}>
            <Chevron />
          </div>
          <span>{displayDate}</span>
          <div role="presentation" onClick={goFront}>
            <Chevron />
          </div>
        </div>
        <div className={styles.days}>
          {makeDays.map((day) => (
            <span key={day}>{day}</span>
          ))}
        </div>
        <div className={styles.dates}>
          {dayGrid().map((day) => {
            const actualDay = momentInstance
              .clone()
              .startOf('month')
              .add(day.value - 1, 'days');
            return day.value === 0 ? (
              <span key={day.id} />
            ) : (
              <Day
                key={day.id}
                isFrom={
                  selectionState.from &&
                  selectionState.from.isSame(actualDay) &&
                  !selectionState.to.isSame(actualDay)
                }
                isFromInterval={actualDay.isBetween(
                  selectionState.from,
                  selectionState.to,
                  'date',
                  '[]',
                )}
                isTo={
                  selectionState.to &&
                  selectionState.to.isSame(actualDay) &&
                  !selectionState.from.isSame(actualDay)
                }
                isEqual={
                  selectionState.from &&
                  selectionState.from.isSame(actualDay) &&
                  selectionState.to.isSame(actualDay)
                }
                selectionActive={selectionState.selectionActive}
                day={day.value}
                onDayClick={dayClick}
                onHoverDay={onHover}
                available={day.available}
              />
            );
          })}
        </div>
      </div>
    );
  }, [
    parentRect.top,
    parentRect.height,
    parentRect.left,
    parentRect.width,
    parentRect.bottom,
    overlayClasses,
    goBack,
    displayDate,
    goFront,
    makeDays,
    dayGrid,
    momentInstance,
    selectionState.from,
    selectionState.to,
    selectionState.selectionActive,
    dayClick,
    onHover,
  ]);

  return createPortal(overlayStructure(), element);
};

export default DateOverlay;
