import React, { useRef, useEffect, useState, useCallback } from 'react';
import { ButtonProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as OkIconSVG } from '../assets/checkp.svg';
import { ReactComponent as FailIconSVG } from '../assets/x.svg';

/**
 * Button with explosion animation.
 *
 * Usage:
 * ```
 * <Button
 *  label="My Button"
 *  type="button"
 *  buttonColor="orange"
 *  dataTestIdButton= identification for test cases
 *  onClick={() => console.log('You clicked me!')}
 * />
 * ```
 */
let interval: number = null;

/**
 * @param buttonColor
 * @param label
 * @param onClick
 * @param type
 * @param isSubmitting
 * @param submitSuccess
 * @param submitFailure
 * @param disabled
 * @param icon
 * @param fullWidth
 * @param isAction
 */
const Button: React.FC<ButtonProps> = (props) => {
  const {
    label,
    onClick,
    type,
    buttonColor,
    isSubmitting,
    submitSuccess,
    submitFailure,
    disabled,
    dataTestIdButton,
    icon: Icon,
    fullWidth,
    isAction,
  } = props;
  const buttonRef: React.Ref<HTMLButtonElement> = useRef(null);
  const [explosionPosition, setExplosionPosition] = useState({
    top: 0,
    left: 0,
  });
  const [loaderWidth, setLoaderWidth] = useState(0);

  const finishSuccess = !isSubmitting && submitSuccess;
  const finishFailure = !isSubmitting && submitFailure;

  const buttonClasses = [
    styles.button,
    fullWidth && !isAction && styles.fullWidth,
    isAction && styles.actionButton,
    Icon && styles.iconButton,
    buttonColor === 'green' && styles.greenButton,
    buttonColor === 'orange' && styles.orangeButton,
    buttonColor === 'white' && styles.whiteButton,
    buttonColor === 'black' && styles.blackButton,
    buttonColor === 'outlined' && styles.outlinedButton,
    (finishSuccess || finishFailure) && styles.transformAnim,
  ].join(' ');

  const loaderClasses = [
    styles.buttonLoading,
    finishFailure && styles.failure,
  ].join(' ');

  const explosionClasses = [
    styles.explosion,
    (finishSuccess || finishFailure) && styles.explosionAnim,
    finishFailure && styles.failure,
    finishSuccess && styles.success,
  ].join(' ');

  const computedPosition = useCallback(() => {
    const rect: DOMRect = buttonRef.current.getBoundingClientRect();
    const top = rect.top + buttonRef.current.clientHeight / 2;
    const left = rect.left + buttonRef.current.clientWidth / 2;
    setExplosionPosition({
      top,
      left,
    });
  }, [buttonRef]);

  useEffect(() => {
    window.addEventListener('resize', computedPosition);
    return () => {
      window.removeEventListener('resize', computedPosition);
    };
  }, [computedPosition]);

  useEffect(() => {
    if (isSubmitting) {
      computedPosition();
      interval = window.setInterval(() => {
        setLoaderWidth((preWidth) => preWidth + 2);
      }, 100);
    } else {
      clearInterval(interval);
      setLoaderWidth(0);
      setTimeout(() => {
        setExplosionPosition({
          top: 0,
          left: 0,
        });
      }, 2800);
    }
  }, [isSubmitting, finishFailure, finishSuccess, computedPosition]);

  return (
    <>
      <button
        ref={buttonRef}
        id="button-id"
        type={type}
        onClick={onClick}
        className={buttonClasses}
        disabled={disabled || isSubmitting || submitSuccess}
        data-testid={dataTestIdButton}
      >
        <div className={styles.buttonContainer}>
          {Icon && <Icon />}
          {label}
          {isSubmitting && (
            <div
              className={loaderClasses}
              style={{
                clipPath: `inset(0 ${100 - loaderWidth}% 0 0)`,
              }}
            >
              {label}
            </div>
          )}
        </div>
      </button>
      <div
        className={explosionClasses}
        data-testid="explosion"
        style={{
          clipPath: explosionPosition.left
            ? `circle(${
                !(finishSuccess || finishFailure) ? '25px' : '100%'
              } at ${explosionPosition.left}px ${explosionPosition.top}px)`
            : 'none',
        }}
      >
        <OkIconSVG
          data-testid="ok-icon"
          style={{
            left: explosionPosition.left - 12.5,
            top: explosionPosition.top - 12.5,
            visibility: finishSuccess ? 'visible' : 'hidden',
          }}
        />
        <FailIconSVG
          data-testid="fail-icon"
          style={{
            left: explosionPosition.left - 12.5,
            top: explosionPosition.top - 12.5,
            visibility: finishFailure ? 'visible' : 'hidden',
          }}
        />
      </div>
    </>
  );
};

export default Button;
