import React, { useCallback } from 'react';
import styles from './styles.module.scss';
import { SliderProps } from './types';
import { ReactComponent as PrevIcon } from '../../../assets/prev-slide.svg';
import { ReactComponent as NextIcon } from '../../../assets/next-slide.svg';
import { ReactComponent as TrashIcon } from '../../../assets/tachito.svg';

const Slider: React.FC<SliderProps> = ({
  slides,
  index,
  readOnly,
  deleteImage,
}) => {
  const [current, setCurrent] = index;
  const { length } = slides;

  const nextSlide = useCallback(() => {
    setCurrent((currCurrent) =>
      currCurrent === length - 1 ? 0 : currCurrent + 1,
    );
  }, [length, setCurrent]);

  const prevSlide = useCallback(() => {
    setCurrent((currCurrent) =>
      currCurrent === 0 ? length - 1 : currCurrent - 1,
    );
  }, [length, setCurrent]);

  const deleteCurrent = useCallback(() => {
    deleteImage(current);
  }, [deleteImage, current]);

  return (
    <div className={`${styles.styledSlider} ${styles.container}`}>
      <div className={`${styles.containerCol1} ${styles.justifyContentCenter}`}>
        {current !== 0 && (
          <div
            role="presentation"
            className={`${styles.arrowButton} ${styles.leftArrow}`}
            onClick={prevSlide}
          >
            <PrevIcon />
          </div>
        )}
      </div>
      <div className={styles.containerCol4}>
        {slides.map(
          (slide, i) =>
            i === current && (
              <div key={slide}>
                <div className={styles.imageContainer}>
                  <img className={styles.slideImage} src={slide} alt={slide} />
                </div>
              </div>
            ),
        )}
      </div>
      <div className={`${styles.containerCol1} ${styles.justifyContentCenter}`}>
        {current !== length - 1 && (
          <div
            role="presentation"
            className={`${styles.arrowButton} ${styles.rightArrow}`}
            onClick={nextSlide}
          >
            <NextIcon />
          </div>
        )}
      </div>
      {!readOnly && (
        <TrashIcon
          role="presentation"
          onClick={deleteCurrent}
          className={styles.trashIcon}
        />
      )}
    </div>
  );
};

export default Slider;
