import React, { useState, useCallback, useEffect, useRef } from 'react';
import { createPortal } from 'react-dom';
import { DropDownProps } from './types';
import styles from './styles.module.scss';
import DropDownOption from '../DropDownOption/DropDownOption';
import { getPropertyByPathString } from '../../utils/arrays';
import { disableScroll, enableScroll } from '../../utils/scroll';

const overlayRoot = document.getElementById('overlay-root');
const OVERFLOW_OFFSET = 20;

const AutoCompleteDropDown: React.FC<DropDownProps> = (props) => {
  const {
    target,
    options,
    idProperty,
    showProperty,
    iconProperty,
    isOpen,
    onSelectOption,
    highlightedIndex,
    setHighlightedIndex,
    testId,
  } = props;

  const [parentRect, setParentRect] = useState({} as DOMRect);
  const [element] = useState(document.createElement('div'));
  const divRef: React.Ref<HTMLDivElement> = useRef(null);

  useEffect(() => {
    if (isOpen) {
      disableScroll();
    } else {
      enableScroll();
    }

    return () => {
      enableScroll();
    };
  }, [isOpen]);

  useEffect(() => {
    if (isOpen) {
      const parent = document.getElementById(target);
      const newRect: DOMRect = parent.getBoundingClientRect();
      setParentRect(newRect);
    }
  }, [isOpen, target]);

  useEffect(() => {
    if (!overlayRoot) {
      return () => {};
    }

    const parent = document.getElementById(target);

    if (parent) {
      const newRect: DOMRect = parent.getBoundingClientRect();
      if (parentRect.left !== newRect.left) {
        setParentRect(newRect);
      }
      overlayRoot.appendChild(element);
    }

    return () => {
      if (overlayRoot.contains(element)) {
        overlayRoot.removeChild(element);
      }
    };
  }, [element, target, parentRect]);

  const overlayClasses = [styles.dropDown, isOpen && styles.show].join(' ');

  const overlayStructure = useCallback(() => {
    const top = parentRect.top + parentRect.height + 5;
    if (Number.isNaN(top)) {
      return null;
    }
    return (
      <div
        ref={divRef}
        className={overlayClasses}
        data-testid={testId}
        style={{
          left: parentRect.left,
          top,
          width: parentRect.width,
          maxHeight: divRef.current
            ? Math.abs(
                window.innerHeight - divRef.current.getBoundingClientRect().top,
              ) - OVERFLOW_OFFSET
            : 'none',
        }}
      >
        {options.map((option, index) => (
          <DropDownOption
            key={getPropertyByPathString(option, idProperty)}
            optionId={getPropertyByPathString(option, idProperty)}
            name={option[showProperty]}
            icon={option[iconProperty]}
            index={index}
            onSelectOption={onSelectOption}
            isHighlighted={highlightedIndex.index === index}
            hoverClass={styles.optionHighlighted}
            setHighlightedIndex={setHighlightedIndex}
          />
        ))}
      </div>
    );
  }, [
    overlayClasses,
    parentRect,
    options,
    showProperty,
    idProperty,
    onSelectOption,
    highlightedIndex,
    setHighlightedIndex,
    testId,
    iconProperty,
  ]);

  return createPortal(overlayStructure(), element);
};

export default AutoCompleteDropDown;
