import React, { useRef, useCallback, useState, useEffect } from 'react';
import { TbodyProps } from './types';
import styles from './styles.module.scss';
import Paginator from '../Paginator/Paginator';

/**
 *
 * @param onScrollBottom For "infinite-scroll" pagination. Event triggerd when scroll to the bottom of the table ()=>Promise<any>
 * @param onChangePage For "paginator" pagination. Event triggered when changing page (page:number)=>Promise<any>
 * @param canFetch If 'false' the pagination events won't be triggered
 * @param isLoadingExternal If 'true' the loader animation will be shown
 * @param noResults Component to be shown when the table has no entries
 * @param classes Array of CSS clases for the component
 * @param rowsCount Its a boolean
 * @param paginator Type of pagination: paginator | infinite-scroll | none
 * @param pages For "paginator" pagination. Object that receives de "totalPages" available and the "currentPage" selected
 * @param t For static texts
 * @param dataTestId Id for tests
 *
 */

const SCROLL_OFFSET = 200;

const Tbody: React.FC<TbodyProps> = (props) => {
  const {
    onScrollBottom,
    onChangePage,
    children,
    canFetch = true,
    isLoadingExternal,
    noResults,
    classes,
    rowsCount = 10,
    paginator = 'none',
    pages: { currentPage, totalPages } = { currentPage: 1, totalPages: 1 },
    dataTestId,
    t,
  } = props;

  const tbodyRef: React.Ref<HTMLTableSectionElement> = useRef(null);
  const onScroll = useCallback(() => {
    const { scrollHeight, scrollTop, clientHeight } = tbodyRef.current;
    if (
      scrollHeight <= scrollTop + clientHeight + SCROLL_OFFSET &&
      !isLoadingExternal &&
      canFetch
    ) {
      onScrollBottom();
    }
  }, [isLoadingExternal, canFetch, onScrollBottom]);

  const onClick = useCallback(
    (page: number) => {
      if (!isLoadingExternal) {
        onChangePage(page);
      }
    },
    [isLoadingExternal, onChangePage],
  );

  return (
    <>
      <tbody
        data-testid={dataTestId}
        className={`${styles.tbody} ${classes && classes.join(' ')}`}
        style={{
          position: 'relative',
          minHeight: '5px',
          maxHeight: rowsCount && 52 * rowsCount,
        }}
        onScroll={
          paginator === 'infinite-scroll' && onScrollBottom ? onScroll : null
        }
        ref={tbodyRef}
      >
        {isLoadingExternal && (
          <tr className={styles.loaderTr}>
            <td>
              <div className={styles.loaderBar}>
                <div className={styles.indeterminate} />
              </div>
            </td>
          </tr>
        )}
        {React.Children.count(children) > 0 && children}
        {React.Children.count(children) === 0 && (
          <tr
            className={styles.noResults}
            style={{ height: noResults ? '400px' : '' }}
          >
            {noResults || <td>{t('GenericErrors:noResults')}</td>}
          </tr>
        )}
      </tbody>
      {paginator === 'paginator' && (
        <tfoot className={styles.justifyContentCenter}>
          <tr>
            <td>
              <Paginator
                onPageClick={onClick}
                currentPage={currentPage}
                totalPages={totalPages}
              />
            </td>
          </tr>
        </tfoot>
      )}
    </>
  );
};

export default Tbody;
