import React from 'react';
import { ArrowButtonProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as ArrowLeft } from '../../../assets/arrow_left.svg';
import { ReactComponent as ArrowLeftDisabled } from '../../../assets/arrow_left_disabled.svg';
import { ReactComponent as ArrowRight } from '../../../assets/arrow_right.svg';
import { ReactComponent as ArrowRightDisabled } from '../../../assets/arrow_right_disabled.svg';

/**
 *
 * @param direction Direction of the arrow
 * @param disabled If 'true' the button won't be clickable
 * @param onClick Button OnClick method
 * @param dataTestId Id for tests
 *
 */

const ArrowButton: React.FC<ArrowButtonProps> = ({
  direction = 'left',
  disabled = false,
  onClick,
  dataTestId,
}) => (
  <>
    {disabled && (
      <div className={styles.arrowButtonDisabled} data-testid={dataTestId}>
        {direction === 'left' ? <ArrowLeftDisabled /> : <ArrowRightDisabled />}
      </div>
    )}
    {!disabled && (
      <div
        onClick={onClick}
        className={styles.arrowButton}
        data-testid={dataTestId}
        role="presentation"
      >
        {direction === 'left' ? <ArrowLeft /> : <ArrowRight />}
      </div>
    )}
  </>
);

export default ArrowButton;
