import React, { useCallback, useState, useRef, useEffect } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { ImageInputProps } from './types';
import { ReactComponent as IcoUpload } from '../assets/ico-new-file.svg';
import { ReactComponent as IcoError } from '../assets/ico-input-error.svg';
import { ReactComponent as Trash } from '../assets/tachito.svg';
import { ReactComponent as Drag } from '../assets/drag-icon.svg';
import styles from './styles.module.scss';
import { ID } from '../utils/arrays';

const ImageInput: React.FC<ImageInputProps> = (props) => {
  const {
    input,
    meta,
    onChange,
    onDelete,
    beforeDelete,
    placeholder,
    disabled,
    helperText,
    t,
    testId,
    accept,
    imageClasses,
    inputClasses,
    multipleImages = null,
    validations = {},
    draggable = false,
  } = props;
  const [file, setFile] = useState<File>(null);
  const [fileBase64, setFileBase64] = useState<string>('');
  const [fileArray, setFileArray] = useState<any[]>([]);
  const [error, setError] = useState(false);
  const fileInputRef: React.Ref<HTMLInputElement> = useRef(null);

  const getImageDimensions = useCallback(
    (image: string) =>
      new Promise((resolved) => {
        const i = new Image();
        i.onload = () => {
          resolved({ width: i.width, height: i.height });
        };
        i.src = image;
      }),
    [],
  );

  const validateImage = useCallback(
    async (image: string) => {
      const {
        format,
        maxSizeKb,
        maxHeight,
        minHeight,
        maxWidth,
        minWidth,
      } = validations;
      const valid = {
        format: true,
        maxSizeKb: true,
        maxHeight: true,
        minHeight: true,
        maxWidth: true,
        minWidth: true,
      };

      const dimensions: {
        width?: number;
        height?: number;
      } = await getImageDimensions(image);
      if (format) {
        valid.format = format.includes(
          image.split(';')[0].split('/')[1].toLowerCase(),
        );
      }

      if (maxSizeKb) {
        const imageLength = image.split(',')[1].length;
        const sizeInBytes = 4 * Math.ceil(imageLength / 3) * 0.5624896334383812;
        const sizeInKb = sizeInBytes / 1024;
        valid.maxSizeKb = sizeInKb <= maxSizeKb;
      }

      if (maxHeight) {
        valid.maxHeight = dimensions.height <= maxHeight;
      }

      if (minHeight) {
        valid.minHeight = dimensions.height > minHeight;
      }

      if (maxWidth) {
        valid.maxWidth = dimensions.width <= maxWidth;
      }

      if (minWidth) {
        valid.minWidth = dimensions.width > minWidth;
      }

      return (
        valid.format &&
        valid.maxSizeKb &&
        valid.maxHeight &&
        valid.minHeight &&
        valid.maxWidth &&
        valid.minWidth
      );
    },
    [getImageDimensions, validations],
  );

  const readFileToString = useCallback(
    (fileP: File) => {
      if (fileP) {
        const reader = new FileReader();
        reader.onload = async (e) => {
          const res = e.target.result as string;
          const valid = await validateImage(res);
          if (valid) {
            setError(false);
            if (!multipleImages) {
              setFileBase64(res);
              setFile(fileP);
              if (onChange) {
                onChange(res);
              }
              input.onChange(res);
            } else {
              const tempFileArray = [...fileArray, { id: ID(), image: res }];
              if (onChange) {
                onChange(tempFileArray.map((item) => item.image));
              }
              input.onChange(tempFileArray.map((item) => item.image));
              setFileArray(tempFileArray);
            }
          } else {
            setError(true);
          }
        };
        reader.readAsDataURL(fileP);
      }
    },
    [input, onChange, fileArray, multipleImages, validateImage],
  );

  const compareFileArrays = useCallback(
    (values) => {
      if (values.length !== fileArray.length) {
        return false;
      }

      let res = true;

      fileArray.forEach((item: any) => {
        if (!values.includes(item.image)) {
          res = false;
        }
      });

      return res;
    },
    [fileArray],
  );

  useEffect(() => {
    if (!input.value) {
      if (multipleImages) {
        setFileArray([]);
      } else {
        setFileBase64('');
        setFile(null);
      }
    } else {
      // eslint-disable-next-line no-lonely-if
      if (multipleImages && !compareFileArrays(input.value)) {
        setFileArray(
          input.value.map((item: string) => ({ id: ID(), image: item })),
        );
      } else {
        setFileBase64(input.value);
        setFile(input.value);
      }
    }
  }, [
    readFileToString,
    input.value,
    multipleImages,
    fileArray,
    compareFileArrays,
  ]);

  const handleFileChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const { files } = event.target;
      readFileToString(files[0]);
      fileInputRef.current.value = null;
    },

    [readFileToString],
  );

  const deleteFile = useCallback(() => {
    input.onBlur();
    setFile(null);
    if (onDelete) {
      onDelete();
    } else {
      if (onChange) {
        onChange(null);
      }
      setFileBase64('');
      input.onChange('');
    }
  }, [onChange, onDelete, input]);

  const handleFileReset = useCallback(() => {
    if (beforeDelete) {
      beforeDelete(deleteFile);
    } else {
      deleteFile();
    }
  }, [deleteFile, beforeDelete]);

  const deleteItem = useCallback(
    (index: number) => () => {
      input.onBlur();
      const tempFileArray = [...fileArray];
      tempFileArray.splice(index, 1);
      if (onDelete) {
        onDelete();
      } else {
        if (onChange) {
          onChange(tempFileArray.map((item) => item.image));
        }
        input.onChange(tempFileArray.map((item) => item.image));
        setFileArray(tempFileArray);
      }
    },
    [onChange, onDelete, input, fileArray],
  );

  const handleItemReset = useCallback(
    (index: number) => () => {
      if (beforeDelete) {
        beforeDelete(deleteItem(index));
      } else {
        deleteItem(index)();
      }
    },
    [beforeDelete, deleteItem],
  );

  const handleClick = useCallback(() => {
    fileInputRef.current.click();
    input.onBlur();
  }, [input]);

  const makeOnDragEndFunction = useCallback(
    (result: any) => {
      const { destination, source } = result;
      if (!destination) {
        return;
      }

      if (
        destination.droppableId === source.droppableId &&
        destination.index === source.index
      ) {
        return;
      }
      const tempFileArray = Array.from(fileArray);
      const mov = tempFileArray.splice(source.index, 1);
      tempFileArray.splice(result.destination.index, 0, mov[0]);
      setFileArray([...tempFileArray]);
      input.onChange(tempFileArray.map((item) => item.image));
      if (onChange) {
        onChange(tempFileArray.map((item) => item.image));
      }
    },
    [fileArray, input, onChange],
  );

  const hasError = meta && meta.invalid;

  return (
    <div className={styles.inputContainer} data-testid={testId}>
      <div className={styles.trashContainer}>
        {!multipleImages && (
          <div className={styles.inputContainer}>
            <div className={styles.trashContainer}>
              <div
                className={[
                  styles.fileInputContainer,
                  file && styles.fileLoaded,
                  inputClasses,
                  (input.disabled || disabled) && styles.disabled,
                ].join(' ')}
                role="presentation"
                onClick={
                  !(input.disabled || disabled) ? handleClick : undefined
                }
              >
                {file ? (
                  <img
                    src={fileBase64}
                    alt={file.name}
                    className={[styles.singleImage, imageClasses].join(' ')}
                  />
                ) : (
                  <>
                    <IcoUpload />
                    {placeholder}
                  </>
                )}
                <input
                  {...input}
                  accept={accept}
                  value={undefined}
                  className={styles.inputFile}
                  ref={fileInputRef}
                  onChange={
                    !(input.disabled || disabled) ? handleFileChange : undefined
                  }
                  type="file"
                  data-testid="file-input-test"
                />
              </div>
            </div>
            {file && !disabled && (
              <span
                role="presentation"
                onClick={handleFileReset}
                className={styles.trash}
              >
                <Trash />
              </span>
            )}
          </div>
        )}
        {multipleImages && (
          <div className={styles.inputContainer}>
            <div className={styles.trashContainer}>
              <DragDropContext onDragEnd={makeOnDragEndFunction}>
                <Droppable droppableId="droppable">
                  {(dropProvided: any) => (
                    <div
                      ref={dropProvided.innerRef}
                      {...dropProvided.droppableProps}
                    >
                      {fileArray.map((item: any, index: number) => (
                        <Draggable
                          key={item.id}
                          draggableId={item.id}
                          index={index}
                        >
                          {(dragProvided: any) => (
                            <div
                              ref={dragProvided.innerRef}
                              {...dragProvided.draggableProps}
                              style={dragProvided.draggableProps.style}
                            >
                              <div
                                {...dragProvided.dragHandleProps}
                                style={{
                                  position: 'absolute',
                                  height: 0,
                                  width: 0,
                                }}
                              />
                              <div className={styles.multipliedImageContainer}>
                                <img
                                  src={item.image}
                                  alt={input.name}
                                  className={[
                                    styles.singleImage,
                                    imageClasses,
                                  ].join(' ')}
                                />
                                {draggable ? (
                                  <span
                                    {...dragProvided.dragHandleProps}
                                    className={styles.trash}
                                  >
                                    <Drag />
                                  </span>
                                ) : (
                                  !disabled && (
                                    <span
                                      onClick={handleItemReset(index)}
                                      className={styles.trash}
                                      role="presentation"
                                    >
                                      <Trash />
                                    </span>
                                  )
                                )}
                              </div>
                            </div>
                          )}
                        </Draggable>
                      ))}
                      {dropProvided.placeholder}
                    </div>
                  )}
                </Droppable>
              </DragDropContext>
              {fileArray.length < multipleImages && !draggable && (
                <div
                  className={[
                    styles.fileInputContainer,
                    inputClasses,
                    (input.disabled || disabled) && styles.disabled,
                  ].join(' ')}
                  role="presentation"
                  onClick={
                    !(input.disabled || disabled) ? handleClick : undefined
                  }
                >
                  <IcoUpload />
                  {placeholder}
                  <input
                    {...input}
                    accept={accept}
                    value={undefined}
                    className={styles.inputFile}
                    ref={fileInputRef}
                    onChange={
                      !(input.disabled || disabled)
                        ? handleFileChange
                        : undefined
                    }
                    type="file"
                    data-testid="file-input-test"
                  />
                </div>
              )}
            </div>
          </div>
        )}
        <span
          className={[
            styles.labelMessage,
            (hasError || error) && meta.touched && styles.errorMessage,
          ].join(' ')}
        >
          {(hasError || error) && meta.touched && <IcoError />}
          {helperText && helperText}
          {hasError && !helperText && t(`Validations:${meta.error}`)}
        </span>
      </div>
    </div>
  );
};

export default ImageInput;
