import React from 'react';
import { PaymentStatus } from '../constants/pickit';
import styles from './styles.module.scss';
import { StatusBadgeProps } from './types';

const badgeClasses: any = {
  [PaymentStatus.Pending]: styles.statusPending,
  [PaymentStatus.Canceled]: styles.statusCancelled,
};

const StatusBadge: React.FC<StatusBadgeProps> = ({ status, testId }) => (
  <div
    className={badgeClasses[status] || styles.statusPending}
    data-testid={testId}
  >
    {status}
  </div>
);

export default StatusBadge;
