import React, { useCallback, useEffect, useRef, useState } from 'react';
import styles from './styles.module.scss';
import { ReactComponent as IcoUpload } from '../../../assets/mdi_add.svg';
import { ReactComponent as TrashIcon } from '../../../assets/tachito.svg';
import { ImageGridProps } from './types';
import { ID } from '../../../utils/arrays';

const ImageGrid: React.FC<ImageGridProps> = ({
  slides,
  index,
  value,
  readOnly,
  onChange,
  placeholder,
  deleteImage,
  setError,
}) => {
  const [current, setCurrent] = index;
  const [, setFile] = value;
  const [reader] = useState(new FileReader());
  const fileInputRef: React.Ref<HTMLInputElement> = useRef(null);

  useEffect(() => {
    reader.addEventListener('load', () => {
      setFile((currFiles) => {
        if (onChange) {
          onChange([reader.result as string, ...currFiles].join(','));
        }
        return [reader.result as string, ...currFiles];
      });
      setCurrent(0);
    });
    return () => {
      reader.removeEventListener('load', () => {});
    };
  }, [reader, setFile, setCurrent, onChange]);

  const readFileToString = useCallback(
    (fileP: File) => {
      if (fileP) {
        reader.readAsDataURL(fileP);
      }
    },
    [reader],
  );

  const handleFileChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const { files } = event.target;
      const ext = files[0].type.split('/').pop();
      const validExt = ['png', 'PNG', 'jpg', 'JPG', 'jpeg', 'JPEG'];

      if (validExt.includes(ext) && files[0].size <= 5242880) {
        setError(false);
        readFileToString(files[0]);
      } else {
        setError(true);
      }
    },

    [readFileToString, setError],
  );

  const handleClick = useCallback(() => {
    fileInputRef.current.click();
  }, []);

  const deleteImageItem = useCallback(
    (i: number) => () => {
      deleteImage(i);
    },
    [deleteImage],
  );

  const setSlide = useCallback(
    (i: number) => () => {
      setCurrent(i);
    },
    [setCurrent],
  );

  return (
    <div className={[styles.containerFluid, styles.gridWrapper].join(' ')}>
      <div className={styles.containerRow}>
        <div className={styles.containerCol9}>
          <div className={styles.containerRow}>
            {!readOnly && (
              <div className={`${styles.containerCol4} ${styles.itemMargin}`}>
                <div className={styles.inputContainer}>
                  <div className={styles.trashContainer}>
                    <div
                      className={[styles.fileInputContainer].join(' ')}
                      role="presentation"
                      onClick={handleClick}
                    >
                      <IcoUpload />
                      <span>{placeholder}</span>
                      <input
                        className={styles.inputFile}
                        ref={fileInputRef}
                        onChange={handleFileChange}
                        type="file"
                        data-testid="file-input-test"
                        accept="image/png,image/jpg,image/jpeg"
                      />
                    </div>
                  </div>
                </div>
              </div>
            )}
            {slides?.map((slide, i) => (
              <div
                key={ID()}
                className={`${styles.containerCol4} ${styles.itemMargin}`}
              >
                <div
                  role="presentation"
                  onClick={setSlide(i)}
                  className={`${styles.imageItem} ${
                    current === i && styles.selected
                  }`}
                >
                  {!readOnly && (
                    <TrashIcon
                      className={styles.trashIcon}
                      onClick={deleteImageItem(i)}
                    />
                  )}
                  <img className={styles.slideImage} src={slide} alt={slide} />
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ImageGrid;
