import React, {
  useState,
  useCallback,
  useRef,
  useEffect,
  useMemo,
} from 'react';
import { AutoCompleteProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as ChevronIcon } from '../assets/chevronA.svg';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';
import { ReactComponent as IconInputError } from '../assets/ico-input-error.svg';
import DropDown from '../SharedDropdown/DropDown/DropDown';
import { ID, getPropertyByPathString } from '../utils/arrays';

/**
 *
 * @param placeholder
 * @param labeled
 * @param options [{id, name}, ...] by default
 * @param idProperty Name of the property asociated to 'id'. Default: 'id'
 * @param showProperty Name of the property asociated to label to show on each option. Default: 'name'
 * @param onChange
 * @param dropDownOnly If 'true' the behavior is like Select component
 * @param readOnly If 'true' the values cant be modified
 * @param disabled Its a boolean
 *
//  */
const AutoComplete: React.FC<AutoCompleteProps> = (props) => {
  const {
    input,
    meta,
    placeholder,
    labeled,
    options = [],
    idProperty = 'id',
    showProperty = 'name',
    classes = [],
    onChange,
    dropDownOnly,
    disabled = false,
    readOnly = false,
    t,
    testId,
  } = props;
  const inputRef: React.Ref<HTMLInputElement> = useRef(null);
  const [filteredOptions, setFilteredOptions] = useState([...options]);
  const [generatedId] = useState(ID());
  const [value, setValue] = useState('');
  const [isOpen, setIsOpen] = useState(false);
  const [highlightedIndex, setHighlightedIndex] = useState({
    index: 0,
    id: '',
  });
  const hasError = useMemo(() => meta && meta.invalid && meta.touched, [meta]);
  const inputClasses = [hasError && styles.inputError].join(' ');
  const labelClasses = [
    labeled && styles.labelUp,
    ((input && input.value) || value) && !labeled && styles.labelNone,
    styles.label,
  ].join(' ');

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const newValue = event.target.value;
      setValue(newValue);
      if (options.length > 0) {
        if (newValue) {
          const newOptions = options.filter((option) =>
            option[showProperty].toLowerCase().includes(newValue),
          );
          setFilteredOptions(newOptions);
          setHighlightedIndex({
            index: 0,
            id:
              newOptions[0] &&
              getPropertyByPathString(newOptions[0], idProperty),
          });
        } else {
          setFilteredOptions(options);
        }
      }
    },
    [options, idProperty, showProperty],
  );
  const onBlur = useCallback(() => {
    const selectedOption = options.find(
      (option) => input.value === getPropertyByPathString(option, idProperty),
    );
    if (selectedOption) {
      setValue(selectedOption[showProperty]);
    } else {
      setValue('');
      setFilteredOptions(options);
    }
    setIsOpen(false);
    input.onBlur();
  }, [input, options, idProperty, showProperty]);
  const onSelectOption = useCallback(
    (id: number | string) => {
      input.onChange(id);
      if (onChange) {
        onChange(id);
      }
    },
    [input, onChange],
  );
  const onKeyPress = useCallback(
    (event: React.KeyboardEvent<HTMLInputElement>) => {
      switch (event.keyCode) {
        case 38:
          if (highlightedIndex.index > 0) {
            setHighlightedIndex({
              index: highlightedIndex.index - 1,
              id: highlightedIndex.id,
            });
          }
          break;
        case 40:
          if (highlightedIndex.index < options.length - 1) {
            setHighlightedIndex({
              index: highlightedIndex.index + 1,
              id: highlightedIndex.id,
            });
          }
          break;
        case 13:
          event.preventDefault();
          onSelectOption(
            getPropertyByPathString(
              filteredOptions[highlightedIndex.index],
              idProperty,
            ),
          );
          setTimeout(() => inputRef.current.blur()); // Horrible
          break;

        default:
          break;
      }
    },
    [
      highlightedIndex,
      options,
      inputRef,
      filteredOptions,
      idProperty,
      onSelectOption,
    ],
  );
  const expand = useCallback(() => {
    inputRef.current.focus();
  }, []);
  const deselect = useCallback(() => {
    setValue('');
    onSelectOption('');
  }, [onSelectOption]);
  const onFocus = useCallback(() => {
    if (!readOnly) setIsOpen(true);
  }, []);

  useEffect(() => {
    setFilteredOptions([...options]);
  }, [options]);

  useEffect(() => {
    if (!input.value) {
      setValue('');
    }
  }, [input.value]);

  useEffect(() => {
    const option = filteredOptions.find((op) => op[idProperty] === input.value);
    if (option && option[showProperty] !== value) {
      setValue(option[showProperty]);
    }
  }, [input.value, showProperty, idProperty, filteredOptions, value]);

  const showIcon = useCallback(() => {
    const showCloseIcon = input.value !== '' && !dropDownOnly;
    if (disabled || readOnly) {
      return <div />;
    }

    if (showCloseIcon) {
      return <CloseIcon onClick={deselect} className={styles.close} />;
    }

    return <ChevronIcon onClick={expand} className={styles.expand} />;
  }, [input, dropDownOnly, disabled, deselect, expand, readOnly]);

  const focusInput = useCallback(() => inputRef.current.focus(), []);

  return (
    <div
      id="autocomplete-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
    >
      <input
        ref={inputRef}
        id={generatedId}
        {...input}
        value={value}
        onChange={handleChange}
        className={inputClasses}
        onKeyDown={onKeyPress}
        onFocus={onFocus}
        onBlur={onBlur}
        autoComplete="off"
        readOnly={dropDownOnly || readOnly}
        disabled={disabled}
        data-testid="input-autocomplete-test"
      />
      <span className={labelClasses} onClick={focusInput} role="presentation">
        {placeholder}
      </span>
      {hasError && (
        <span className={styles.errorMessage}>
          <IconInputError />
          {t(`Validations:${meta.error}`)}
        </span>
      )}
      {showIcon()}
      <DropDown
        target={generatedId}
        options={filteredOptions}
        value={value}
        isOpen={isOpen}
        idProperty={idProperty}
        showProperty={showProperty}
        onSelectOption={onSelectOption}
        highlightedIndex={highlightedIndex}
        setHighlightedIndex={setHighlightedIndex}
        testId="autocomplete-dropdown-test"
      />
    </div>
  );
};

export default AutoComplete;
