import React, { useRef, useCallback } from 'react';
import { CheckboxProps, CheckboxOwnProps } from './types';
import styles from './styles.module.scss';
/**
 * Checkbox.
 *
 * Usage:
 * ```
 * <Checkbox
 *  label=Name to especify the value once is checked
 *  input= obeject from React Final Form
 *  value= indicates if it's ckecked or not
 *  meta= que es ?
 *  onClick={() => console.log('You clicked me!')}
 * />
 * ```
 
 */
const Checkbox: React.FC<CheckboxProps | CheckboxOwnProps> = (props) => {
  const { label, input, value, onChange, dataTestId, disabled } = props;
  const inputRef: React.Ref<HTMLInputElement> = useRef(null);
  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      if (onChange) {
        onChange(event.target.checked);
      }
      if (input) {
        input.onChange(event.target.checked);
      }
    },
    [input, onChange],
  );
  const handleSpan = useCallback(() => {
    inputRef.current.click();
  }, [inputRef]);

  return (
    <div id="checkbox-id" className={styles.container} data-testid={dataTestId}>
      <input
        {...input}
        ref={inputRef}
        checked={input ? input.value : value}
        id="checkbox"
        type="checkbox"
        onChange={handleChange}
        disabled={disabled}
      />
      <span onClick={handleSpan} role="presentation" />
      {label && <label htmlFor="checkbox">{label}</label>}
    </div>
  );
};

export default Checkbox;
