import React from 'react';
import { BadgeProps } from './types';
import styles from './styles.module.scss';

const COLOR_CLASSES = {
  grey: styles.grey,
  green: styles.green,
  blue: styles.blue,
  red: styles.red,
  orange: styles.orange,
};

const Badge: React.FC<BadgeProps> = ({ title, color, classes, testId }) => {
  const badgeClasses = [styles.badge, COLOR_CLASSES[color]].concat(classes);

  return (
    <div className={badgeClasses.join(' ')} data-testid={testId}>
      {title}
    </div>
  );
};

export default Badge;
