B,iBAAkB,CAEnB,wCGhFO,UApBgB,CHsGxB,yBACE,wBAAmB,CAAnB,qBAAmB,CAAnB,kBAAmB,CACnB,oDAAmC,CAAnC,4CAAmC,CACnC,mBAAa,CAAb,mBAAa,CAAb,YAAa,CACb,uBAAiB,CAAjB,oBAAiB,CAAjB,sBAAuB,CAMxB,wCG5FO,aA/Cc,CHiItB,6BAQI,iBAAkB,CACnB,uBAID,WAAY,CACZ,gBAAiB,CAClB,OAGC,wBAAyB,CACzB,WAAY,CACb,yCAGC,GACE,iCAAW,CAAX,yBAA0B,CAE5B,IACE,kCAAW,CAAX,0BAA2B,CAE7B,IACE,iCAAW,CAAX,yBAA0B,CAE5B,IACE,kCAAW,CAAX,0BAA2B,CAE7B,KACE,iCAAW,CAAX,yBAA0B,CAAA,CAhB7B,iCAGC,GACE,iCAAW,CAAX,yBAA0B,CAE5B,IACE,kCAAW,CAAX,0BAA2B,CAE7B,IACE,iCAAW,CAAX,yBAA0B,CAE5B,IACE,kCAAW,CAAX,0BAA2B,CAE7B,KACE,iCAAW,CAAX,yBAA0B,CAAA","sourcesContent":["@import '@pickit/pickit-grid-system';\n@import 'src/colors';\n\n.container {\n  overflow: scroll;\n  @include container(12, 32px);\n\n  @include media(md-down) {\n    @include container(12, 20px);\n  }\n}\n\n.image-item {\n  align-items: center;\n  border-radius: 10px;\n  cursor: pointer;\n  display: flex;\n  justify-content: center;\n  outline: 2px solid;\n  overflow: hidden;\n  padding-top: 100%;\n  position: relative;\n  @include theme-selector('outline-color', 'color-pinkish-grey', true);\n\n  & img {\n    bottom: 0;\n    left: 0;\n    min-height: 100%;\n    position: absolute;\n    right: 0;\n    top: 0;\n    width: 100%;\n  }\n}\n\n.trash-icon {\n  position: absolute;\n  right: 4px;\n  top: 4px;\n  z-index: 2;\n}\n\n.selected {\n  @include theme-selector('outline-color', 'color-orange', true);\n}\n\n.grid-wrapper {\n  position: absolute;\n  top: 2px;\n  width: 100%;\n}\n\n.warning {\n  display: flex;\n\n  &-text {\n    font-size: 12px;\n    font-weight: 400;\n    margin-left: 5px;\n  }\n}\n\n.error-text {\n  @include theme-selector('color', 'color-error', true);\n}\n\n.item-margin {\n  margin-bottom: 32px;\n\n  @include media(md) {\n    margin-bottom: 20px;\n  }\n\n  @include media(md-down) {\n    margin-bottom: 20px;\n  }\n}\n\n// IMAGE INPUT\n\n.input-container {\n  display: flex;\n  flex-direction: row;\n  height: 100%;\n  margin-bottom: 10px;\n  text-align: center;\n}\n\n.trash-container {\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 0px !important;\n  width: 100%;\n}\n\n.file-input-container {\n  align-items: center;\n  border-radius: 10px;\n  box-sizing: border-box;\n  cursor: pointer;\n  display: flex;\n  flex: 1;\n  flex-direction: column;\n  font-size: 12px;\n  font-weight: 700;\n  justify-content: space-evenly;\n  outline: 2px dashed;\n  overflow: hidden;\n  width: 100%;\n  @include theme-selector('background', 'color-white', true);\n  @include theme-selector('outline-color', 'switch-label-grey', true);\n  @include theme-selector('color', 'color-orange', true);\n\n  img {\n    width: 100%;\n  }\n\n  &.disabled {\n    cursor: not-allowed;\n  }\n\n  & > span {\n    padding: 0px 16%;\n  }\n}\n\n.input-file {\n  height: 0;\n  position: absolute;\n  width: 0;\n}\n\n.label-message {\n  font-size: 10px;\n  font-weight: 600;\n  letter-spacing: 0.015em;\n  line-height: 13px;\n  text-align: center;\n  @include theme-selector('color', 'switch-grey', true);\n}\n\n.error-message {\n  align-items: center;\n  animation: errorBounce 250ms linear;\n  display: flex;\n  justify-content: center;\n  @include theme-selector('color', 'color-red', true);\n\n  svg {\n    margin-right: 12px;\n  }\n}\n\n.trash {\n  height: 20px;\n  margin-left: 12px;\n}\n\nbutton {\n  background-color: inherit;\n  border: none;\n}\n\n@keyframes errorBounce {\n  0% {\n    transform: translateX(5px);\n  }\n  25% {\n    transform: translateX(-5px);\n  }\n  50% {\n    transform: translateX(5px);\n  }\n  75% {\n    transform: translateX(-5px);\n  }\n  100% {\n    transform: translateX(5px);\n  }\n}\n","@import \"./media\";\r\n\r\n@mixin comun($gutter) {\r\n  position: relative;\r\n  width: 100%;\r\n  box-sizing: border-box;\r\n  padding-left: $gutter;\r\n}\r\n\r\n@mixin create-col-classes($modifier, $grid-cols, $breakpoint, $gutter) {\r\n  @include create-mq($breakpoint) {\r\n    &-col#{$modifier}-auto {\r\n      @include comun($gutter);\r\n      @include make-col-auto();\r\n    }\r\n    &-col#{$modifier} {\r\n      @include comun($gutter);\r\n      flex-basis: 0;\r\n      flex-grow: 1;\r\n      max-width: 100%;\r\n    }\r\n    @for $i from 1 through $grid-cols {\r\n      &-col#{$modifier}-#{$i} {\r\n        @include comun($gutter);\r\n        @include make-col($i, $grid-cols);\r\n      }\r\n\r\n      &-col#{$modifier}-offset-#{$i} {\r\n        @include comun($gutter);\r\n        @include make-col-offset($i, $grid-cols);\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n@mixin container($grid__cols, $gutter, $grid__width: \"\") {\r\n  @if $grid__width != \"\" {\r\n    max-width: calc(#{$grid__width});\r\n    margin: 0 auto;\r\n  }\r\n\r\n  &-fluid {\r\n    margin: 0;\r\n    max-width: 100%;\r\n  }\r\n\r\n  &-row {\r\n    box-sizing: border-box;\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    width: calc(100% + #{$gutter});\r\n    margin-left: calc(-#{$gutter});\r\n  }\r\n\r\n  @each $modifier, $breakpoint in $map-grid-props {\r\n    @include create-col-classes($modifier, $grid__cols, $breakpoint, $gutter);\r\n  }\r\n}\r\n\r\n@mixin make-col($size, $columns: $grid-columns) {\r\n  flex: 0 0 percentage($size / $columns);\r\n  margin-left: 0;\r\n  max-width: percentage($size / $columns);\r\n}\r\n\r\n@mixin make-col-auto() {\r\n  flex: 0 0 auto;\r\n  width: auto;\r\n  max-width: 100%;\r\n  margin-left: 0;\r\n}\r\n\r\n@mixin make-col-offset($size, $columns: $grid-columns) {\r\n  $num: $size / $columns;\r\n  margin-left: if($num == 0, 0, percentage($num));\r\n}\r\n","$xs: 375;\r\n$sm: 667;\r\n$md: 1280;\r\n$lg: 1440;\r\n$xl: 1920;\r\n\r\n$map-grid-props: (\r\n  '': 0,\r\n  '-xs': $xs,\r\n  '-sm': $sm,\r\n  '-md': $md,\r\n  '-lg': $lg,\r\n  '-xl': $xl,\r\n);\r\n\r\n@mixin media($media) {\r\n  @if $media == xs {\r\n    @media screen and (max-width: calc(#{$sm}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == sm {\r\n    @media screen and (min-width: $sm*1px) and (max-width: calc(#{$md}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == md {\r\n    @media screen and (min-width: $md*1px) and (max-width: calc(#{$lg}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == lg {\r\n    @media screen and (min-width: $lg*1px) and (max-width: calc(#{$xl}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == xl {\r\n    @media screen and (min-width: $xl*1px) {\r\n      @content;\r\n    }\r\n  } @else if $media == xl-down {\r\n    @media screen and (max-width: calc(#{$xl}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == lg-down {\r\n    @media screen and (max-width: calc(#{$lg}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == md-down {\r\n    @media screen and (max-width: calc(#{$md}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  } @else if $media == sm-down {\r\n    @media screen and (max-width: calc(#{$sm}*1px - 1px)) {\r\n      @content;\r\n    }\r\n  }\r\n}\r\n\r\n@mixin create_mq($breakpoint) {\r\n  @if ($breakpoint == 0) {\r\n    @content;\r\n  } @else {\r\n    @media screen and (min-width: $breakpoint *1px) {\r\n      @content;\r\n    }\r\n  }\r\n}","$themes: (\n  default: (\n    color-modal-grey: #6d6e70,\n    color-background: #fafafa,\n    color-real-black: #000000,\n    color-budget-grey: #666666,\n    color-pinkish-grey: #cccccc,\n    color-light-grey: #e9e9e9,\n    color-green: #63e8a9,\n    color-green-switch: #3fd78d,\n    color-green-loading: #6ddaa7,\n    color-white: #ffffff,\n    color-red: #ff1515,\n    color-black: #333333,\n    color-black-degrade: #00000040,\n    color-modal-background: #00000087,\n    color-orange: #ff6c0e,\n    color-faq-orange: #ff6c0d,\n    color-orange-dark: #db5e0e,\n    color-font: #4d4d4d,\n    color-font-2: #2d2d2d,\n    color-title: #101820,\n    color-pearl: #95e1bf,\n    color-light-blue: #00b2e3,\n    color-light-blue-alt: #e9f1f4,\n    color-header-table: #f7f7f7,\n    color-loader: #ff9e7d,\n    color-cerulean: #1aa7e8,\n    badge-green: #d9ffec,\n    badge-red: #fff1f0,\n    badge-blue: #e9f1f4,\n    badge-grey: #f9f9f9,\n    badge-orange: #fff6e9,\n    sla-border: #e5e5e5,\n    card-red: #ff8672,\n    card-green: #96e1c0,\n    card-purple: #bcb6ff,\n    card-orange: #ffac76,\n    switch-green: #04ce6d,\n    switch-grey: #808080,\n    switch-green-disabled: #7ae1b0,\n    switch-grey-disabled: #b5b5b5,\n    switch-toggle-green: #e6faf0,\n    switch-toggle-grey: #f0f0f0,\n    switch-label-grey: #b8b8b8,\n    color-modal-success: #04ce6d,\n    color-modal-warning: #ffa825,\n    color-modal-error: #ff1515,\n    color-modal-info: #338af3,\n    color-gray-hover: #dddddd,\n    color-header-table2: #f5f5f5,\n  ),\n  dark: (),\n);\n\n@mixin theme-selector($key, $color, $isModule) {\n  @each $theme-name, $theme-color in $themes {\n    @if $isModule {\n      #{':global'}(.theme-#{$theme-name}) & {\n        #{$key}: map-get(map-get($themes, $theme-name), $color);\n      }\n    } @else {\n      .theme-#{$theme-name} & {\n        #{$key}: map-get(map-get($themes, $theme-name), $color);\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
