import React, { useState, useEffect, useCallback } from 'react';
import { CollapsibleProps } from './types';
import styles from './styles.module.scss';

const Collapsible: React.FC<CollapsibleProps> = (props) => {
  const { children, openState, onClick, maxOpenHeight = 500, testId } = props;
  if (
    React.Children.count(children) < 2 ||
    React.Children.count(children) > 2
  ) {
    throw new Error(
      'The collapsible element must have only 2 child, the first is the toggler element and the second is the content of the toggle',
    );
  }
  const [isOpen, setIsOpen] = useState(openState);
  const tootleCollapsible = useCallback(() => {
    setIsOpen(!isOpen);
    if (onClick && typeof onClick === 'function') {
      onClick(!isOpen);
    }
  }, [isOpen, onClick]);
  const collapsibleContentClasses = [styles.collapsibleContent].join(' ');

  const contentStyles = {
    maxHeight: isOpen ? maxOpenHeight : 0,
  };
  const childrens = React.Children.map(children, (child) => child);
  useEffect(() => {
    setIsOpen(openState);
  }, [openState]);

  return (
    <div id="collapsible-id" data-testid={testId}>
      <div
        role="presentation"
        onClick={tootleCollapsible}
        data-testid="toggle-test"
      >
        {childrens[0]}
      </div>
      <div
        style={contentStyles}
        className={collapsibleContentClasses}
        data-testid="content-test"
      >
        {childrens[1]}
      </div>
    </div>
  );
};

export default Collapsible;
