import React, { useEffect, useState, useCallback, useMemo } from 'react';
import moment from 'moment';
import styles from './styles.module.scss';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';
import { DatePickerProps } from './types';
import { ID } from '../utils/arrays';
import DateOverlay from './components/DateOverlay/DateOverlay';

/**
 * DatePickier
 * @param placeholder Represent the placeholder string when the input is empty
 * @param labeled
 * @param minDate The minimum date that the date picker can't access
 * @param maxDate The maximum date that the date picker can't access
 * @param unavailableDates An array of dates that the date picker can't access
 * @param singleSelection A boolean that sleect one date only
 */
const DatePicker: React.FC<DatePickerProps> = (props) => {
  const {
    input,
    placeholder,
    labeled,
    classes = [],
    singleSelection,
    disabled,
    readOnly,
    minDate,
    outputFormat = 'DD/MM/YYYY',
    meta,
    t,
    testId,
    language,
  } = props;
  const [momentInstance, setInstance] = useState(null);
  const [infoData, setInfoData] = useState({
    displayDate: '',
  });
  const [generatedId] = useState(ID());
  const [value, setValue] = useState(
    input.value &&
      (singleSelection
        ? input.value
        : `De ${input.value.from} a ${input.value.until}`),
  );
  const [isOpen, setIsOpen] = useState(false);
  useEffect(() => {
    if (singleSelection) setValue(input.value || '');
    else {
      setValue(
        input.value?.from && input.value?.until
          ? `De ${input.value.from} a ${input.value.until}`
          : '',
      );
    }
  }, [input.value, singleSelection]);
  const handleClick = useCallback(() => {
    if (!disabled && !readOnly) setIsOpen(true);
  }, [disabled, readOnly]);
  const handleChange = useCallback(
    (date: any) => {
      if (singleSelection) {
        setIsOpen(false);
        input.onChange(moment(date.from, 'DD/MM/YYYY').format(outputFormat));
      } else {
        setIsOpen(false);
        input.onChange({
          from: moment(date.from, 'DD/MM/YYYY').format(outputFormat),
          until: moment(date.to, 'DD/MM/YYYY').format(outputFormat),
        });
      }
    },
    [input, outputFormat, singleSelection],
  );
  const makeDays = useMemo(() => {
    if (!momentInstance) return [];
    const weekDays: Array<number> = [0, 1, 2, 3, 4, 5, 6];
    return weekDays.map((day: number) =>
      momentInstance.clone().weekday(day).format('dd'),
    );
  }, [momentInstance]);

  const isAvailable = useCallback(
    (num: number) => {
      const actualDay = momentInstance
        .clone()
        .startOf('month')
        .add(num - 1, 'days');
      if (minDate && actualDay.isBefore(moment(minDate))) {
        return false;
      }
      return true;
    },
    [minDate, momentInstance],
  );

  const createGrid = useCallback(() => {
    const firstDayDate = moment(momentInstance).startOf('month');
    const initialEmptyCells = firstDayDate.weekday();
    const lastDayDate = moment(momentInstance).endOf('month');
    const lastEmptyCells = 6 - lastDayDate.weekday();
    const daysInMonth = momentInstance.daysInMonth();
    const arrayLength = initialEmptyCells + lastEmptyCells + daysInMonth;
    const gridArr = [];
    for (let i = 0; i < arrayLength; i += 1) {
      const obj: any = {};
      if (i < initialEmptyCells || i > initialEmptyCells + daysInMonth - 1) {
        obj.value = 0;
        obj.available = false;
      } else {
        obj.value = i - initialEmptyCells + 1;
        obj.available = isAvailable(i - initialEmptyCells + 1);
      }
      obj.id = ID();
      gridArr.push(obj);
    }
    return gridArr;
  }, [momentInstance, isAvailable]);

  const goBack = useCallback(() => {
    momentInstance.add(-1, 'month');
    setInfoData({
      displayDate: momentInstance.format('MMMM YYYY'),
    });
  }, [momentInstance]);
  const goFront = useCallback(() => {
    momentInstance.add(1, 'month');
    setInfoData({
      displayDate: momentInstance.format('MMMM YYYY'),
    });
  }, [momentInstance]);

  useEffect(() => {
    const newInstance = moment(new Date());
    newInstance.locale(language);
    setInfoData({
      displayDate: newInstance.format('MMMM YYYY'),
    });
    setInstance(newInstance);
  }, [isOpen, language]);

  const hasError = meta && meta.invalid && meta.touched;
  const inputClasses = [hasError && styles.inputError].join(' ');

  const deselect = useCallback(() => {
    if (singleSelection) {
      input.onChange('');
    } else {
      input.onChange({
        from: '',
        until: '',
      });
    }
  }, [singleSelection, input]);

  const labelClasses = [
    labeled && styles.labelUp,
    value && !labeled && styles.labelNone,
    styles.label,
  ].join(' ');

  return (
    <div
      id="datepicker-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
    >
      {/*     {input.value && labeled && (
        <span className={styles.label}>{placeholder}</span>
      )} */}
      <input
        id={generatedId}
        {...input}
        readOnly
        disabled={disabled}
        value={value}
        className={inputClasses}
        onMouseDown={handleClick}
        autoComplete="off"
        data-testid="date-picker-input"
      />
      <span className={labelClasses}>{placeholder}</span>
      {hasError && (
        <span className={styles.errorMessage} data-testid="span-test">
          {t(`Validations:${meta.error}`)}
        </span>
      )}
      {input.value !== '' && value !== '' && !readOnly && !disabled && (
        <CloseIcon onClick={deselect} className={styles.close} />
      )}
      <DateOverlay
        isOpen={isOpen}
        momentInstance={momentInstance}
        dayGrid={createGrid}
        makeDays={makeDays}
        target={generatedId}
        goBack={goBack}
        goFront={goFront}
        setIsOpen={setIsOpen}
        onSelection={handleChange}
        singleSelection={singleSelection}
        displayDate={infoData.displayDate}
      />
    </div>
  );
};

export default DatePicker;
