import React, {
  useCallback,
  useMemo,
  useState,
  useEffect,
  useRef,
} from 'react';
import { FieldRenderProps } from 'react-final-form';
import { TextAreaInputProps } from './types';
import styles from './styles.module.scss';
import { ReactComponent as CloseIcon } from '../assets/ico-close.svg';
import { ReactComponent as IconInputError } from '../assets/ico-input-error.svg';

const TextInput: React.FC<
  | TextAreaInputProps
  | FieldRenderProps<any, HTMLInputElement | HTMLTextAreaElement>
> = (props) => {
  const {
    input,
    meta,
    placeholder,
    labeled,
    onChange,
    value,
    icon: Icon,
    classes = [],
    readOnly,
    disabled,
    theme,
    testId,
    maxRows,
    minRows,
    t,
  } = props;

  const textAreaRef = useRef<HTMLTextAreaElement | null>(null);
  const [textAreaHeight, setTextAreaHeight] = useState<string | number>(24);
  const [parentHeight, setParentHeight] = useState(48);
  const [maxHeight] = useState(maxRows ? maxRows * 24 : 24);
  const [minHeight] = useState(minRows ? minRows * 24 : 24);

  const hasError = useMemo(() => meta && meta.invalid && meta.touched, [meta]);

  const errorData = meta && meta.error && meta.error.split(':');

  const inputClasses = [
    hasError && styles.inputError,
    theme === 'light' && styles.inputLight,
    theme === 'light' && hasError && styles.inputErrorLight,
  ].join(' ');

  const labelClasses = [
    labeled && styles.labelUp2,
    ((input && input.value) || value) && labeled && styles.labelUp,
    styles.label,
  ].join(' ');

  useEffect(() => {
    if (textAreaRef.current!.scrollHeight) {
      setParentHeight(textAreaRef.current!.scrollHeight + 24);
      setTextAreaHeight(textAreaRef.current!.scrollHeight - 10);
    }
    if (input.value) {
      setParentHeight(textAreaRef.current!.scrollHeight + 24);
      setTextAreaHeight(textAreaRef.current!.scrollHeight - 10);
    }
  }, [input.value]);

  const handleChange = useCallback(
    (event: React.ChangeEvent<HTMLTextAreaElement>) => {
      setTextAreaHeight(textAreaRef.current!.scrollHeight - 10);
      setParentHeight(textAreaRef.current!.scrollHeight + 24);

      if (input) {
        input.onChange(event.target.value);
      }
      if (onChange) {
        onChange(event.target.value, input && input.name);
      }
    },
    [input, onChange],
  );

  const cleanValue = useCallback(() => {
    setTextAreaHeight(24);
    setParentHeight(48);
    input.onChange('');
    if (onChange) {
      onChange('');
    }
  }, [input, onChange]);

  const focusInput = useCallback(() => textAreaRef.current.focus(), []);

  const showIcon = useCallback(() => {
    if (disabled || readOnly) {
      return null;
    }

    if (Icon) {
      return <Icon />;
    }

    if (hasError) {
      return (
        <IconInputError onClick={cleanValue} className={styles.cleanIcon} />
      );
    }

    if (input.value || value) {
      return <CloseIcon onClick={cleanValue} className={styles.cleanIcon} />;
    }

    return null;
  }, [Icon, cleanValue, input.value, value, hasError, disabled, readOnly]);

  return (
    <div
      id="textarea-input-id"
      className={[styles.inputContainer, ...classes].join(' ')}
      data-testid={testId}
      style={{
        height: parentHeight,
        maxHeight: maxHeight + 24,
        minHeight: minHeight + 24,
      }}
    >
      <textarea
        {...input}
        readOnly={readOnly || disabled}
        disabled={disabled}
        value={input ? input.value : value}
        onChange={handleChange}
        className={inputClasses}
        data-testid="textarea-input-id"
        ref={textAreaRef}
        style={{
          height: textAreaHeight,
          maxHeight,
          minHeight,
        }}
      />
      <span className={labelClasses} onClick={focusInput} role="presentation">
        {placeholder}
      </span>
      {hasError && (
        <span
          className={
            theme === 'light' ? styles.errorMessageLight : styles.errorMessage
          }
        >
          {t(`Validations:${errorData[0]}`, { data: errorData[1] })}
        </span>
      )}
      {showIcon()}
    </div>
  );
};

export default TextInput;
