import {
  useEffect,
  useState,
  FC,
  useCallback,
  MouseEvent as ReactMouseEvent,
} from 'react';
import { SimpleButtonProps } from './types';
import styles from './styles.module.scss';

/**
 * Simple Button without explosion animation.
 *
 * Usage:
 * ```
 * <SimpleButton
 *  label="My Button"
 *  type="button"
 *  buttonColor="orange"
 *  onClick={() => console.log('You clicked me!')}
 * />
 * ```
 * @param label String label
 * @param type button|submit
 * @param buttonColor
 * @param onClick
 *
 */
const SimpleButton: FC<SimpleButtonProps> = (props) => {
  const {
    label,
    onClick,
    type,
    buttonColor,
    isSubmitting,
    disabled,
    icon: Icon,
    classes = [],
    testID,
  } = props;
  const [loaderWidth, setLoaderWidth] = useState(0);

  const buttonClasses = [
    styles.button,
    Icon && styles.iconButton,
    buttonColor === 'green' && styles.greenButton,
    buttonColor === 'white' && styles.whiteButton,
    buttonColor === 'orange' && styles.orangeButton,
    buttonColor === 'black' && styles.blackButton,
    buttonColor === 'outlined' && styles.outlineOrange,
    buttonColor === 'success' && styles.successButton,
    buttonColor === 'warning' && styles.warningButton,
    buttonColor === 'error' && styles.errorButton,
    buttonColor === 'info' && styles.infoButton,
    ...classes,
  ].join(' ');

  const loaderClasses = [styles.buttonLoading].join(' ');

  const handleClick = useCallback(
    (event: ReactMouseEvent<HTMLButtonElement, MouseEvent>) => {
      if (type === 'button' && !onClick) {
        console.warn(
          'If your type is set to "button", you must provide an onClick function',
        );
      } else {
        onClick(event);
      }
    },
    [onClick, type],
  );

  useEffect(() => {
    let timer: number;
    let interval: number = null;
    if (isSubmitting) {
      interval = window.setInterval(() => {
        setLoaderWidth((preWidth) => preWidth + 2);
      }, 900);
    } else if (interval) {
      clearInterval(interval);
      setLoaderWidth(100);
      timer = window.setTimeout(() => setLoaderWidth(0), 1800);
    }
    return () => {
      clearInterval(interval);
      clearTimeout(timer);
      setLoaderWidth(0);
    };
  }, [isSubmitting]);

  return (
    <button
      data-testid={testID}
      type={type}
      onClick={handleClick}
      className={buttonClasses}
      disabled={disabled || isSubmitting}
    >
      {Icon && (
        <div className={styles.icon}>
          <Icon />
        </div>
      )}
      {label}
      <div
        className={loaderClasses}
        style={{
          clipPath: `inset(0 ${100 - loaderWidth}% 0 0)`,
        }}
      >
        {label}
      </div>
    </button>
  );
};

export default SimpleButton;
