import React, { useCallback, useState } from 'react';
import { Form as FinalForm } from 'react-final-form';
import { FormProps } from './types';

const Form: React.FC<FormProps> = ({
  render,
  onSubmit,
  onSuccess,
  onFailure,
  children,
  mutators,
  initValues,
  isAnimated,
  resetOnSubmit,
}) => {
  const [{ isFailure, isSubmitting, isSuccess }, setSubmitStatus] = useState({
    isSubmitting: false,
    isSuccess: false,
    isFailure: false,
  });
  const handleRender = useCallback(
    (props) => {
      const renderFunction = render || (children as any);
      return renderFunction({
        ...props,
        isSubmitting,
        isSuccess,
        isFailure,
      });
    },
    [render, isSubmitting, isSuccess, isFailure, children],
  );
  const handleSubmit = useCallback(
    (values, form) => {
      setSubmitStatus({
        isSubmitting: true,
        isSuccess: false,
        isFailure: false,
      });
      onSubmit(values)
        .then((response) => {
          setSubmitStatus({
            isSubmitting: false,
            isSuccess: true,
            isFailure: false,
          });
          if (typeof onSuccess === 'function') {
            if (isAnimated) {
              setTimeout(() => onSuccess(response), 2800);
            } else {
              onSuccess(response);
            }
          }
          if (resetOnSubmit) {
            form.reset();
            Object.keys(initValues).forEach((key) => form.resetFieldState(key));
          }
        })
        .catch((error) => {
          setSubmitStatus({
            isSubmitting: false,
            isSuccess: false,
            isFailure: true,
          });
          if (typeof onFailure === 'function') {
            if (isAnimated) {
              setTimeout(() => onFailure(error), 2800);
            } else {
              onFailure(error);
            }
          }
        });
    },
    [onSubmit, onSuccess, resetOnSubmit, isAnimated, initValues, onFailure],
  );

  return (
    <>
      <FinalForm
        onSubmit={handleSubmit}
        render={handleRender}
        mutators={{
          ...mutators,
        }}
        initialValues={initValues}
      />
    </>
  );
};

export default Form;
