import React, {
  ReactNode,
  RefObject,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import { CarrouselProps, ChildMappedProps } from './types';
import styles from './styles.module.scss';
import CarrouselCard from './components/CarrouselCard';

function getPrevElement(list: ChildMappedProps[]) {
  const sibling = list[0].child.previousElementSibling;

  if (sibling instanceof HTMLElement) {
    return sibling;
  }

  return sibling;
}

function getNextElement(list: ChildMappedProps[]) {
  const sibling = list[list.length - 1].child.nextElementSibling;

  if (sibling instanceof HTMLElement) {
    return sibling;
  }

  return null;
}

function usePosition(ref: RefObject<HTMLDivElement>) {
  const [prevElement, setPrevElement] = useState(null);
  const [nextElement, setNextElement] = useState(null);
  const [frames, setFrames] = useState<ChildMappedProps[]>([]);

  const updateFrames = useCallback((newFrames: ChildMappedProps[]) => {
    setFrames(newFrames);
  }, []);

  const activeFrame = useCallback((index: number) => {
    setFrames((currFrames) => {
      const newFrames = [...currFrames];
      for (let i = 0; i < currFrames.length; i += 1) {
        newFrames[i].active = i === index;
      }
      return newFrames;
    });
  }, []);

  useEffect(() => {
    const element = ref.current;
    let scrollTimer = -1;

    const calculateVisibleElements = () =>
      Array.from(element.children)
        .map((child, index) => ({ child, iIndex: index }))
        .filter(({ child }) => {
          const rect = element.getBoundingClientRect();
          const childRect = child.getBoundingClientRect();
          return childRect.left >= rect.left && childRect.right <= rect.right;
        });

    let visibleElements = calculateVisibleElements();

    // In a future, if it wish, calculate frames seen exactly what amount of child it's shown in e=the frame, then separate in a right way.
    // In fact, just show the number of frames that is equal to NUMBER of shown in a frame divided by TOTAL children; but knowing exactly /// in each frame
    updateFrames(
      Array.from(element.children).map((child, index) => ({
        child,
        iIndex: index,
        active: false,
      })),
    );

    const setAnimation = () => {
      Array.from(element.children).forEach((child) => {
        const currentChild = child;

        const arrayClass = currentChild.className.split(' ');
        const index = arrayClass.findIndex(
          (sClass: string) => sClass === styles.elementActive,
        );

        if (index >= 0) {
          arrayClass.splice(index, 1);
          currentChild.className = `${arrayClass.join(' ')}`;
        }
      });

      if (visibleElements.length) {
        const centerElement =
          visibleElements[Math.floor(visibleElements.length / 2)];

        if (centerElement) {
          centerElement.child.className = `${styles.elementActive} ${centerElement.child.className}`;
          activeFrame(centerElement.iIndex);
        }
      } else if (Array.from(element.children).length) {
        const centerElement = Array.from(element.children)[
          element.children.length - 1
        ];
        centerElement.className = `${styles.elementActive} ${centerElement.className}`;
        activeFrame(element.children.length - 1);
      }
    };

    const update = () => {
      visibleElements = calculateVisibleElements();

      if (visibleElements.length > 0) {
        setPrevElement(getPrevElement(visibleElements) || element.lastChild);
        setNextElement(getNextElement(visibleElements) || element.firstChild);
      } else if (Array.from(element.children).length) {
        setPrevElement(element.lastChild.previousSibling);
        setNextElement(element.firstChild);
      }

      if (scrollTimer !== -1) {
        clearTimeout(scrollTimer);
      }

      scrollTimer = window.setTimeout(setAnimation, 100);
    };

    update();

    element.addEventListener('scroll', update, { passive: true });

    return () => {
      element.removeEventListener('scroll', update);
    };
  }, [ref, activeFrame, updateFrames]);

  const scrollToElement = useCallback(
    (element) => {
      const currentNode = ref.current;
      const currentElement = element;

      if (!currentNode || !currentElement) return;

      const newScrollPosition =
        currentElement.offsetLeft +
        currentElement.getBoundingClientRect().width / 2 -
        currentNode.getBoundingClientRect().width / 2;

      currentNode.scroll({
        left: newScrollPosition,
        behavior: 'smooth',
      });
    },
    [ref],
  );

  const scrollRight = useCallback(() => scrollToElement(nextElement), [
    scrollToElement,
    nextElement,
  ]);

  const scrollLeft = useCallback(() => scrollToElement(prevElement), [
    scrollToElement,
    prevElement,
  ]);

  return {
    scrollToElement,
    frames,
    scrollRight,
    scrollLeft,
  };
}

/**
 *
 * @param cards Arrays of ReactNodes
 *
 *
 */

const Carrousel: React.FC<CarrouselProps> = ({ cards }) => {
  const ref = useRef<HTMLDivElement>();

  const { frames, scrollRight, scrollToElement } = usePosition(ref);

  const scrollTo = useCallback(
    (child) => () => {
      scrollToElement(child);
    },
    [scrollToElement],
  );

  useEffect(() => {
    const interval = setInterval(() => {
      scrollRight();
    }, 5000);
    return () => {
      clearInterval(interval);
    };
  }, [scrollRight]);

  return (
    <div
      className={styles.carouselContainer}
      role="region"
      aria-label="carousel"
    >
      <div className={styles.carouselContainerInner} ref={ref}>
        {cards &&
          cards.map((card: ReactNode) => <CarrouselCard>{card}</CarrouselCard>)}
      </div>
      <div className={styles.buttonContainers}>
        {frames.map((frame) => (
          <button
            key={frame.iIndex}
            className={frame.active ? styles.active : undefined}
            onClick={scrollTo(frame.child)}
            type="button"
          >
            {' '}
          </button>
        ))}
      </div>
    </div>
  );
};

export default Carrousel;
