import React, { useCallback, useRef } from 'react';
import { ToolTipProps } from './types';
import styles from './styles.module.scss';

export enum ToolTipPosition {
  right = 'right',
  bottom = 'bottom',
  left = 'left',
  top = 'top',
}

const mapPositionToClass = {
  right: [styles.tooltipContentRight, styles.tooltipArrowRight],
  left: [styles.tooltipContentLeft, styles.tooltipArrowLeft],
  top: [styles.tooltipContentTop, styles.tooltipArrowTop],
  bottom: [styles.tooltipContentBottom, styles.tooltipArrowBottom],
};

const Tooltip = ({
  children,
  message,
  disabled,
  position = ToolTipPosition.bottom,
}: ToolTipProps) => {
  const tipRef = useRef<HTMLDivElement | null>(null);
  const childRef = useRef<HTMLDivElement | null>(null);

  const getPosition = useCallback(() => {
    if (!childRef.current || !tipRef.current) return null;

    const { x, y, width, height } = childRef.current.getBoundingClientRect();
    const {
      width: tipWidth,
      height: tipHeight,
    } = tipRef.current.getBoundingClientRect();

    switch (position) {
      case ToolTipPosition.bottom:
        return {
          top: `${y + height + 12}px`,
          left: `${x + width / 2}px`,
        };
      case ToolTipPosition.top:
        return {
          top: `${y - tipHeight - 12}px`,
          left: `${x + width / 2}px`,
        };
      case ToolTipPosition.left:
        return {
          top: `${y + height / 2 - tipHeight / 2}px`,
          left: `${x - tipWidth - 12}px`,
        };
      case ToolTipPosition.right:
        return {
          top: `${y + height / 2 - tipHeight / 2}px`,
          left: `${x + width + 12}px`,
        };
      default:
        return {
          top: '0px',
          left: '0px',
        };
    }
  }, [position]);

  const handleMouseEnter = useCallback(() => {
    if (!tipRef.current || disabled) return;
    tipRef.current.style.display = 'flex';
    const pos = getPosition();
    if (pos) {
      tipRef.current.style.top = pos.top;
      tipRef.current.style.left = pos.left;
      tipRef.current.style.opacity = '1';
    }
  }, [disabled, getPosition]);

  const handleMouseLeave = useCallback(() => {
    if (tipRef.current && childRef.current && !disabled) {
      tipRef.current.style.opacity = '0';
      tipRef.current.style.display = 'none';
    }
  }, [disabled]);

  const contentClasses = [
    styles.tooltipContent,
    mapPositionToClass[position][0],
  ].join(' ');

  const arrowClasses = [
    styles.tooltipArrow,
    mapPositionToClass[position][1],
  ].join(' ');

  return (
    <>
      <div data-testid="tooltip-root" className={styles.tooltipContainer}>
        <div
          ref={childRef}
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          {children}
        </div>
      </div>
      <div data-testid="tooltip-label" className={contentClasses} ref={tipRef}>
        <div className={arrowClasses} />
        {message}
      </div>
    </>
  );
};

export default Tooltip;
