import React, { useCallback, useEffect, useRef, useState } from 'react';
import Button from '../../../Button/Button';
import { EmptyStatusProps } from './types';
import { ReactComponent as PlaceHolder } from '../../../assets/photoPlaceholder.svg';
import { ReactComponent as WarningIcon } from '../../../assets/warning.svg';
import styles from './styles.module.scss';

export const EmptyStatus: React.FC<EmptyStatusProps> = ({
  helperText,
  readOnly = false,
  value,
  t,
  onChange,
}) => {
  const [, setFile] = value;
  const [reader] = useState(new FileReader());
  const [error, setError] = useState(false);
  const fileInputRef: React.Ref<HTMLInputElement> = useRef(null);

  useEffect(() => {
    reader.addEventListener('load', () => {
      setFile((currFiles) => {
        if (onChange) {
          onChange([reader.result as string, ...currFiles].join(','));
        }
        return [reader.result as string, ...currFiles];
      });
    });

    return () => {
      reader.removeEventListener('load', () => {});
    };
  }, [reader, setFile, onChange]);

  const readFileToString = useCallback(
    (fileP: File) => {
      if (fileP) {
        reader.readAsDataURL(fileP);
      }
    },
    [reader],
  );

  const handleFileChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const { files } = event.target;
      const ext = files[0].type.split('/').pop();
      const validExt = ['png', 'PNG', 'jpg', 'JPG', 'jpeg', 'JPEG'];

      if (validExt.includes(ext) && files[0].size <= 5242880) {
        setError(false);
        readFileToString(files[0]);
      } else {
        setError(true);
      }
    },

    [readFileToString],
  );

  const handleClick = useCallback(() => {
    fileInputRef.current.click();
  }, []);

  return (
    <div>
      <div className={styles.alignCenter} style={{ marginTop: 30 }}>
        <PlaceHolder />
        <div className={styles.emptyStatusText}>
          <div className={styles.imageTextBold} style={{ marginTop: 30 }}>
            {t('MultiImageInput:PlaceholderTitle')}
          </div>
          <div className={styles.helperText}>{helperText}</div>
          {!readOnly && (
            <>
              <div
                className={[styles.disclaimer, error && styles.errorText].join(
                  ' ',
                )}
              >
                {error && <WarningIcon />}
                {`${t('MultiImageInput:ImageTypes')} ${t(
                  'MultiImageInput:ImageSize',
                )}`}
              </div>
              <div className={styles.justifyContentCenter}>
                <div className={styles.actionBtn}>
                  <Button
                    type="button"
                    buttonColor="orange"
                    label={t('MultiImageInput:UploadImage')}
                    onClick={handleClick}
                  />
                  <input
                    className={styles.inputFile}
                    ref={fileInputRef}
                    onChange={handleFileChange}
                    type="file"
                    data-testid="file-input-test"
                    accept="image/png,image/jpg,image/jpeg"
                  />
                </div>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};
export default EmptyStatus;
