import React, { useCallback } from 'react';
import { FloatingButtonProps } from './types';
import styles from './styles.module.scss';
// import { Position } from '../constants/pickit';

/**
 * Floating Button.
 *
 * Usage:
 * ```
 * <FloatingButton
 *  icon= URL for src attribute
 *  position= prop to specify styles on ClassName
 *  dataTestId= used to give an identification to use in unit tests
 *  onClick={() => console.log('You clicked me!')}
 * />
 * ```
 */
const FloatingButton: React.FC<FloatingButtonProps> = (props) => {
  const {
    onClick,
    icon,
    position,
    dataTestId,
    classes = [],
    imageSize,
  } = props;

  const positionsClasses = [styles.button, styles[position], ...classes].join(
    ' ',
  );
  const handleClick = useCallback(() => {
    onClick();
  }, [onClick]);
  return (
    <div
      className={[styles.floatingButton, positionsClasses].join(' ')}
      role="presentation"
      onClick={handleClick}
      data-testid={dataTestId}
    >
      <img
        data-testid="img-id"
        alt="ICON"
        src={icon}
        style={imageSize ? { height: imageSize } : { height: '90%' }}
      />
    </div>
  );
};

export default FloatingButton;
