import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { createPortal } from 'react-dom';
import { ModalProps } from './types';
import styles from './styles.module.scss';

let modalRoot = document.getElementById('overlay-root');
const stopPropagation = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) =>
  event.stopPropagation();

const Modal: React.FC<ModalProps> = (props) => {
  const {
    children,
    width,
    height,
    isOpen,
    onClose,
    testId,
    closeOnBlur = true,
  } = props;
  const [element] = useState(document.createElement('div'));
  const modalStyles = useMemo(
    () => ({
      width,
      height,
    }),
    [height, width],
  );
  const modalContainerClases = [
    styles.modalContainer,
    isOpen ? styles.modalShow : '',
  ].join(' ');

  const modalClases = [styles.modal, isOpen ? styles.modalContent : ''].join(
    ' ',
  );

  useEffect(() => {
    if (!modalRoot) {
      modalRoot = document.createElement('div');
      modalRoot.setAttribute('id', 'overlay-root');
      document.body.appendChild(modalRoot);
    }
    modalRoot.appendChild(element);
    return () => {
      modalRoot.removeChild(element);
    };
  }, [element]);

  const modalStructure = useCallback(
    () => (
      <div
        className={modalContainerClases}
        onClick={closeOnBlur ? onClose : undefined}
        role="presentation"
        data-testid={testId}
      >
        <div
          onClick={stopPropagation}
          className={modalClases}
          style={modalStyles}
          role="presentation"
        >
          {children}
        </div>
      </div>
    ),
    [
      modalStyles,
      children,
      modalClases,
      modalContainerClases,
      onClose,
      closeOnBlur,
      testId,
    ],
  );

  return createPortal(modalStructure(), element);
};

export default Modal;
